<template>
  <div>
    <el-table
      :data="info"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        fixed
        label="科目"
        prop="subject.subject"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        fixed
        label="班级"
        prop="classname"
        :show-overflow-tooltip="true"
        align="center"
        width="130"
      />
      <el-table-column
        label="教室"
        prop="classroom.name"
        :show-overflow-tooltip="true"
        align="center"
        width="80"
      />
      <el-table-column
        label="课时"
        prop="keshi"
        :show-overflow-tooltip="true"
        align="center"
        width="80"
      />
      <el-table-column
        label="课时费"
        prop="money"
        :show-overflow-tooltip="true"
        align="center"
        width="80"
      />
      <el-table-column
        label="额定人数"
        prop="number"
        :show-overflow-tooltip="true"
        align="center"
        width="90"
      />
      <el-table-column
        label="已配置人数"
        prop="count"
        :show-overflow-tooltip="true"
        align="center"
        width="110"
      />
      <el-table-column
        label="剩余名额"
        prop="num"
        :show-overflow-tooltip="true"
        align="center"
        width="120"
      />
      <el-table-column
        label="开放选课"
        :show-overflow-tooltip="true"
        align="center"
        width="120"
      >
        <template slot-scope="scope">
          {{ scope.row.kaifang === 1? '开放' : '不开放' }}
        </template>
      </el-table-column>
      <el-table-column
        label="上课时间"
        prop="emergencyContactName"
        :show-overflow-tooltip="true"
        align="center"
        width="170"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.classroominfo.day ===1">
            星期一
          </span>
          <span v-if="scope.row.classroominfo.day ===2">
            星期二
          </span>
          <span v-if="scope.row.classroominfo.day ===3">
            星期三
          </span>
          <span v-if="scope.row.classroominfo.day ===4">
            星期四
          </span>
          <span v-if="scope.row.classroominfo.day ===5">
            星期五
          </span>
          <span v-if="scope.row.classroominfo.day ===6">
            星期六
          </span>
          <span v-if="scope.row.classroominfo.day ===7">
            星期日
          </span> {{ scope.row.classroominfo.start }}--{{ scope.row.classroominfo.jieshu }}
        </template>
      </el-table-column>
      <el-table-column
        label="年龄下限"
        prop="min_age"
        :show-overflow-tooltip="true"
        align="center"
        width="120"
      />
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            修改
          </el-button>

          <el-button
            size="mini"
            type="primary"
            @click="handleMerge(scope.row.id,scope.row.classname)"
          >
            合并
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleAdd(scope.row.id,scope.row.classname)"
          >
            添加
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row.id)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="合并班级"
      :visible.sync="mergeClass"
      width="30%"
    >
      <el-form>
        <el-form-item label="当前班级">
          <el-input
            v-model="className"
            disabled
            style="width:200px"
          />
        </el-form-item>
        <el-form-item label="目标班级">
          <el-select
            v-model="className2"
            placeholder="请先选择班级"
          >
            <el-option
              v-for="item in className2Options"
              :key="item.id"
              :label="item.classname"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="mergeClass = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="submit"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    info: [Array, String],
  },
  data() {
    return {
      mergeClass: false,
      id: '',
      className: '',
      className2: '',
      className2Options: [],
    };
  },
  methods: {
    handleEdit(id) {
      this.$router.push(`/admin/teditclass/${id}`);
    },
    handleAdd(id, name) {
      this.$router.push(`/admin/tclassaddstu/${id}/${name}`);
    },
    handleDel(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteClassinfo.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getClassInfo');
          } else {
            this.$message.success('失败');
          }
        });
      // this.$router.push(`/admin/tstudentsel/${id}/${name}`);
    },
    handleMerge(id, name) {
      this.id = id;
      this.className = name;
      this.mergeClass = true;
      this.getClass();
    },
    getClass() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassinfos.action',
      })
        .then((res) => {
          this.className2Options = res.data;
        });
    },
    submit() {
      this.$http({
        method: 'POST',
        url: '/pjsys/mergeClassinfo',
        params: {
          classinfo_id: this.className2,
          classinfo_id_clear: this.id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.mergeClass = false;
            this.$emit('getClassInfo');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

