<template>
  <el-row
    :gutter="20"
    style="margin-top:20px"
  >
    <el-col :span="6">
      <el-date-picker
        v-model="date"
        type="date"
        placeholder="选择日期"
        @change="changeDate"
        value-format="yyyy-MM-dd"
      />
    </el-col>
    <el-col :span="4">
      <el-button
        @click="exportAll"
        type="primary"
      >
        导出全部班级
      </el-button>
    </el-col>
    <el-col :span="4">
      <el-button
        type="primary"
        @click="exportClass"
      >
        导出当前班级
      </el-button>
    </el-col>
    <el-col
      :span="10"
    >
      <slot />
    </el-col>
  </el-row>
</template>

<script>
export default {
  props: {
    flag: [String, Number],
    id: [String, Number],
  },
  data() {
    return {
      date: '',
    };
  },
  methods: {
    // 导出全部班级
    exportAll() {
      // console.log(this.date);
      if (this.date === '') {
        window.location.href = `download1.action?flag=${this.flag}`;
      } else {
        window.location.href = `download1.action?flag=${this.flag}?dt=${this.date}`;
      }
    },
    exportClass() {
      if (this.id !== '') {
        if (this.date === '') {
          window.location.href = `downMoney.action?flag=${this.flag}?classinfo_id=${this.id}`;
        } else {
          window.location.href = `downMoney.action?flag=${this.flag}?classinfo_id=${this.id}?dt=${this.date}`;
        }
      } else {
        this.$message.error('请选择班级');
      }
    },
    changeDate() {
      // window.location.href = `download1.action?flag=${this.flag}`;
    },
  },
};
</script>

