<template>
  <div>
    <m-header @getRoom="getRoom" />
    <m-room
      :room="room"
      v-loading="loading"
    />
  </div>
</template>

<script>
import MRoom from './Room.vue';
import MHeader from './Header.vue';

export default {
  components: {
    MRoom,
    MHeader,
  },
  data() {
    return {
      room: [],
      loading: true,
    };
  },
  mounted() {
    this.getRoom();
  },
  methods: {
    getRoom() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassroom.action',
      })
        .then((res) => {
          this.room = res.data;
          this.loading = false;
        });
    },
  },
};
</script>

