<template>
  <div>
    <el-table
      :data="student"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label="姓名"
        prop="stuName"
        sortable
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="性别"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.sex ===1?'男':'女' }}
        </template>
      </el-table-column>
      <el-table-column
        label="身份证号"
        prop="personalNum"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="操作"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            @click="sel(scope.row.id)"
          >
            选课
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    student: [String, Array],
  },
  methods: {
    sel(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/addInfo.action',
        params: {
          classinfo_id: this.$route.params.id,
          stu_id: id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
          } else if (res.data === 3) {
            this.$message.error('该班级名额已满,无法继续添加');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

