<template>
  <div>
    <el-table
      :data="classInfo"
      style="width: 40%;margin-top:20px"
      @selection-change="selection"
    >
      <el-table-column
        type="selection"
      />
      <el-table-column
        label="班级名"
        prop="classname"
        :show-overflow-tooltip="true"
        align="center"
      />
    </el-table>
    <el-row style="margin-top:20px">
      <el-col :span="3">
        <el-button
          type="primary"
          @click="flag(8)"
        >
          已申请续费
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="flag(2)"
        >
          已申请缴费
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          @click="back"
        >
          返回
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      classInfo: [],
      ids: [],
    };
  },
  mounted() {
    this.getClassInfo();
  },
  methods: {
    getClassInfo() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassinfos.action',
      })
        .then((res) => {
          this.classInfo = res.data;
        });
    },
    selection(selection) {
      // console.log(selection);
      this.ids = selection.map(res => res.id);
    },
    flag(flag) {
      this.$http({
        method: 'POST',
        url: '/pjsys/infoUpdateClassinfo_idsByflag',
        params: {
          classinfo_ids: this.ids.toString(),
          my_flag: flag,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
          } else {
            this.$message.error('失败');
          }
        });
    },
    back() {
      this.$router.push('/admin/tclass');
    },
  },
};
</script>

