<template>
  <el-table
    :data="info"
    style="width: 100%;margin-top:20px"
  >
    <el-table-column
      fixed
      label="已选科目"
      prop="classinfo.subject.subject"
      width="90"
      show-overflow-tooltip
      align="center"
    />
    <el-table-column
      label="班级"
      prop="classinfo.classname"
      show-overflow-tooltip
      align="center"
    />
    <el-table-column
      label="面试"
      show-overflow-tooltip
      align="center"
    >
      <template slot-scope="scope">
        <span v-if="scope.row.classinfo.subject.interview ===1">
          需要面试
        </span>
        <span v-if="scope.row.classinfo.subject.interview ===0">
          不需要面试
        </span>
      </template>
    </el-table-column>
    <el-table-column
      label="缴费状态"
      show-overflow-tooltip
      align="center"
    >
      <template slot-scope="scope">
        <span v-if="scope.row.flag ===1">
          已报名未缴费
        </span>
        <span v-if="scope.row.flag ===2">
          申请缴费
        </span>
        <span v-if="scope.row.flag ===3">
          已缴费
        </span>
        <span v-if="scope.row.flag ===4">
          退班
        </span>
        <span v-if="scope.row.flag ===5">
          申请退费
        </span>
        <span v-if="scope.row.flag ===6">
          已退费
        </span>
        <span v-if="scope.row.flag ===7">
          已报名未续费
        </span>
        <span v-if="scope.row.flag ===8">
          申请续费
        </span>
        <span v-if="scope.row.flag ===9">
          已续费
        </span>
        <span v-if="scope.row.flag ===10">
          拒绝缴费
        </span>
        <span v-if="scope.row.flag ===11">
          拒绝续费
        </span>
        <span v-if="scope.row.flag ===12">
          拒绝退费
        </span>
      </template>
    </el-table-column>
    <el-table-column
      label="操作"
      align="center"
    >
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          @click="handleDel(scope.row.id)"
        >
          删除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    info: [Array],
  },
  methods: {
    handleDel(id) {
      this.$http({
        method: 'POST',
        url: '/deleteById.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getInfo');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

