<template>
  <div>
    <export-log />
    <el-table
      :data="stuList"
      class="table"
      v-loading="loading"
    >
      <el-table-column
        fixed
        :show-overflow-tooltip="true"
        prop="teacher.username"
        label="用户名"
        align="center"
        width="100"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        prop="mokuai"
        align="center"
        label="操作模块"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        width="280"
        prop="eventType"
        align="center"
        label="操作类型"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        prop="time"
        align="center"
        width="200"
        label="操作时间"
      />
    </el-table>
    <div class="left">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import ExportLog from './ExportLog.vue';

export default {
  components: {
    ExportLog,
  },
  data() {
    return {
      loading: true,
      stuList: [],
      total: 0,
    };
  },
  mounted() {
    this.getLog();
  },
  // beforeRouteUpdate(next) {
  //   next();
  //   // this.getLog();
  // },
  methods: {
    // 获取所有日志
    getLog(page = 1) {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllLog.action',
        params: {
          page,
        },
      })
        .then((res) => {
          this.loading = false;
          this.stuList = res.data.datas;
          this.total = Number(res.data.total);
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '获取信息失败',
            type: 'error',
          });
        });
    },
    handleCurrentChange(page) {
      this.getLog(page);
    },
  },
};
</script>

<style scoped>
.table{
  width: 100%;
  margin-top: 20px;
}
.left{
  margin-top: 20px;
  float: right;
}
</style>
