<template>
  <div>
    <el-row>
      <el-col :span="4">
        <p>开学通知头部内容:</p>
      </el-col>
      <el-col :span="6">
        <el-input
          placeholder="请输入头部内容"
          v-model="header"
        />
      </el-col>
      <el-col :span="5">
        <el-button
          type="primary"
          @click="update"
        >
          更新
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      header: '',
    };
  },
  mounted() {
    this.getHeader();
  },
  methods: {
    update() {
      this.$http({
        method: 'POST',
        url: '/pjsys/submitNotice',
        params: {
          content: this.header,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.getHeader();
          } else {
            this.$message.success('失败');
          }
        });
    },
    getHeader() {
      this.$http({
        method: 'GET',
        url: '/pjsys/firstNoticeMessage',
      })
        .then((res) => {
          this.header = res.data.content;
        });
    },
  },
};
</script>

