<template>
  <div>
    <m-header @getStudent="getStudent" />
    <m-table
      :student="student"
      @getStudent="getStudent"
    />
    <el-pagination
      style="margin-top:20px;float:right"
      background
      layout="prev, pager, next,jumper"
      :total="total"
      @current-change="page"
      :current-page="pages"
    />
  </div>
</template>

<script>
import MHeader from './AddStuHeader.vue';
import MTable from './AddStuTable.vue';

export default {
  components: {
    MHeader,
    MTable,
  },
  data() {
    return {
      student: [],
      total: 0,
      pages: 0,
    };
  },
  mounted() {
    this.getStudent();
  },
  methods: {
    getStudent() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findByClassinfoId.action',
        params: {
          classinfo_id: this.$route.params.id,
          page: this.pages,
        },
      })
        .then((res) => {
          this.student = res.data.datas;
          this.total = res.data.total;
        });
    },
    page(page) {
      this.pages = page;
      this.getStudent();
    },
  },
};
</script>

