<template>
  <div>
    <el-row
      style="margin-bottom:20px"
      align="middle"
    >
      <el-col :span="6">
        <el-input
          placeholder="请输入学生姓名首字母"
          v-model="name"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="upDataStudent"
        >
          更新学生
        </el-button>
      </el-col>
    </el-row>
    <el-table
      :data="student"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column
        prop="student.stuName"
        label="姓名"
        width="100"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="student.personalNum"
        label="身份证号"
        width="160"
      />
      <el-table-column
        label="性别"
        :show-overflow-tooltip="true"
        align="center"
        width="100"
      >
        <template scope="scope">
          <span v-if="scope.row.student.sex ===1">
            男
          </span>
          <span v-if="scope.row.student.sex ===2">
            女
          </span>
        </template>
      </el-table-column>
      <el-table-column
        prop="classinfo.classname"
        :show-overflow-tooltip="true"
        align="center"
        label="班级"
        width="130"
      />
      <el-table-column
        label="操作"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,1,0)"
          >
            正常
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,1)"
          >
            病假
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,2)"
          >
            事假
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,3)"
          >
            考级或培训
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,4)"
          >
            其他
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="float:right;margin-top:20px">
      <el-pagination
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
export default {
// findAllClassinfos.action
  data() {
    return {
      name: '',
      student: [],
      page: 1,
      pages: 0,
      loading: false,
    };
  },
  mounted() {
  },
  methods: {
    // 确定搜索
    search() {
      if (this.name) {
        this.getStu();
      } else {
        this.$message.error('请输入学生姓名首字母');
      }
    },
    // 学生首字母
    getStu(page = 1) {
      this.loading = true;
      this.$http({
        method: 'POST',
        url: '/pjsys/spellFindByClassinfoId',
        params: {
          spell_stu_name: this.name,
          page,
        },
      })
        .then((res) => {
          this.loading = false;
          if (res.data.datas.length > 0) {
            this.student = res.data.datas;
            this.pages = res.data.pages;
          } else {
            this.$message.error('搜索不到该学生!');
          }
        })
        .catch(() => {
          this.loading = false;
          this.$message.error('搜索出错啦!');
        });
    },
    handleEdit(id, classID, att, reason) {
      this.$http({
        method: 'POST',
        url: 'pjsys/attence.action',
        params: {
          stu_id: id,
          attence_status: att,
          classinfo_id: classID,
          reason_ids: reason,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '考勤成功',
              type: 'success',
            });
            this.getStu(this.page);
          } else {
            this.$message({
              message: '考勤失败',
              type: 'error',
            });
          }
        });
    },
    handleCurrentChange(page) {
      this.page = page;
      this.getStu(page);
    },
    upDataStudent() {
      this.loading = true;
      this.$http({
        method: 'GET',
        url: '/pjsys/updateAllDictionary',
      })
        .then((res) => {
          this.loading = false;
          if (res.data === 1) {
            this.$message.success('成功');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

