<template>
  <div>
    <el-row
      style="margin-bottom:20px"
    >
      <el-col :span="3">
        <el-button
          type="primary"
          @click="exports"
        >
          导出excel
        </el-button>
      </el-col>
      <el-col :span="12">
        <el-date-picker
          v-model="date"
          type="date"
          placeholder="选择日期"
          @change="changeDate"
          value-format="yyyy-MM-dd"
        />
        <el-button
          type="primary"
          @click="changeDate"
        >
          按照日期查询
        </el-button>
      </el-col>
    </el-row>
    <el-row
      style="margin-bottom:20px"
      :gutter="20"
    >
      <el-col :span="10">
        <el-card class="box-card">
          <div
            slot="header"
          >
            <span>总收入</span>
          </div>
          <div>
            {{ allMoney }}
          </div>
        </el-card>
      </el-col>
      <el-col :span="10">
        <el-card class="box-card">
          <div
            slot="header"
          >
            <span>退款金额</span>
          </div>
          <div>
            {{ refund }}
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      date: '',
      refund: '',
      allMoney: '',
    };
  },
  mounted() {
    this.getAllMoney();
    this.getRefund();
  },
  // get
  methods: {
    // 获取总收入
    getAllMoney(date) {
      this.$http.get('/findAllMoney.action', {
        params: {
          dateTime: date,
        },
      })
        .then((res) => {
          this.allMoney = res.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 获取退费
    getRefund(date) {
      this.$http.get('/pjsys/findAllBackmoney.action', {
        params: {
          dateTime: date,
        },
      })
        .then((res) => {
          this.refund = res.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 根据日期查询
    changeDate() {
      this.getAllMoney(this.date);
      this.getRefund(this.date);
    },
    // 导出
    exports() {
      window.location.href = 'pjsys/downloadMoney.action';
    },
  },

};
</script>

