<template>
  <div>
    <el-table
      :data="stu"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        fixed
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(
              scope.row.student.id,
              scope.row.student.user.id,
              scope.row.id,
              2,6)"
          >
            支付宝
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(
              scope.row.student.id,
              scope.row.student.user.id,
              scope.row.id,
              1,6)"
          >
            微信
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(
              scope.row.student.id,
              scope.row.student.user.id,
              scope.row.id,
              3,6)"
          >
            现金
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(
              scope.row.student.id,
              scope.row.student.user.id,
              scope.row.id,
              4,6)"
          >
            POS
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(
              scope.row.student.id,
              scope.row.student.user.id,
              scope.row.id,
              1,12)"
          >
            拒绝
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        fixed
        prop="student.stuName"
        label="姓名"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="家长姓名"
        prop="student.parent"
        :show-overflow-tooltip="true"
        align="center"
        width="90"
      />
      <el-table-column
        prop="classinfo.classname"
        :show-overflow-tooltip="true"
        align="center"
        label="班级"
        width="130"
      />
      <el-table-column
        label="手机号"
        prop="student.mobilephone"
        :show-overflow-tooltip="true"
        align="center"
        width="120"
      />
      <el-table-column
        prop="student.personalNum"
        :show-overflow-tooltip="true"
        label="身份证号"
        width="170"
      />
      <el-table-column
        prop="classinfo.subject.subject"
        :show-overflow-tooltip="true"
        align="center"
        label="科目"
        width="80"
      />
      <el-table-column
        label="申请时间"
        width="120"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template scope="scope">
          {{ scope.row.s_t_time === null ? '暂无时间':scope.row.s_t_time }}
        </template>
      </el-table-column>
      <el-table-column
        prop="classinfo.money"
        :show-overflow-tooltip="true"
        align="center"
        label="费用"
        width="80"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="备注"
        width="130"
      >
        <template scope="scope">
          {{ scope.row.flags === null ? '暂无':scope.row.flags }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="remarks(scope.row.id)"
          >
            备注
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="备注"
      :visible.sync="dialogFormVisible"
    >
      <el-input
        type="textarea"
        autosize
        placeholder="请输入内容"
        v-model="value"
      />
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="submit"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    stu: [String, Array],
  },
  data() {
    return {
      teacherID: '',
      dialogFormVisible: false,
      value: '',
      Id: '',
    };
  },
  mounted() {
    this.teacherID = this.$Storage.get('teacherID');
    if (this.teacherID === '' || this.teacherID === undefined) {
      this.$message.error('没有登录,请登录');
      this.$router.push('/adminlogin');
    }
  },
  methods: {
    handleEdit(stuIDs, ids, userID, pay, flag) {
      this.zf(userID, stuIDs, ids, pay, flag);
    },
    // 拒绝10  3缴费 9续费
    // 1 微信 2 支付宝 3现金 4 POS
    zf(ids, stuIDs, userID, pay, flag) {
      this.$http({
        method: 'POST',
        url: '/pjsys/pay.action',
        params: {
          ids,
          flag,
          stu_ids: stuIDs,
          user_ids: userID,
          teacher_id: this.teacherID,
          pay,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getStu');
          } else {
            this.$message.error('失败');
          }
        })
        .catch(() => {
          this.$message.error('获取失败');
        });
    },
    remarks(id) {
      this.dialogFormVisible = true;
      this.Id = id;
    },
    submit() {
      this.$http({
        method: 'POST',
        url: '/pjsys/updateFlag.action',
        params: {
          id: this.Id,
          flags: this.value,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.dialogFormVisible = false;
            this.$emit('getStu');
          } else {
            this.$message({
              message: '备注失败',
              type: 'error',
            });
          }
        })
        .catch(() => {
          this.$message({
            message: '备注失败',
            type: 'error',
          });
        });
    },
  },
};
</script>

