<template>
  <div>
    <m-header @getSubject="getSubject" />
    <m-table
      :subject="subject"
      @getSubject="getSubject"
    />
  </div>
</template>

<script>
import MHeader from './Header.vue';
import MTable from './Table.vue';

export default {
  components: {
    MHeader,
    MTable,
  },
  data() {
    return {
      subject: [],
    };
  },
  mounted() {
    this.getSubject();
  },
  methods: {
    getSubject() {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllSubject.action',
      })
        .then((res) => {
          this.subject = res.data;
        });
    },
  },
};
</script>

