<template>
  <div>
    <el-row
      style="margin-bottom:20px"
    >
      <el-col :span="4">
        <el-input
          v-model="addName"
          @keyup.enter.native="add"
          placeholder="请输入学科名"
        />
      </el-col>
      <el-col :span="4">
        <el-button
          @click="add"
          type="primary"
        >
          新增学科
        </el-button>
      </el-col>
    </el-row>
    <el-table
      :data="disciline"
      style="width: 100%"
    >
      <el-table-column
        prop="major"
        label="学科"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="操作"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"
          >
            修改
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="修改学科"
      :visible.sync="dialogFormVisible"
    >
      <el-input
        v-model="updataName"
        autocomplete="off"
      />
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="updata"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addName: '',
      disciline: [],
      dialogFormVisible: false,
      updataName: '',
      updataId: '',
    };
  },
  mounted() {
    this.get();
  },
  methods: {
    get() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllMajors.action',
      })
        .then((res) => {
          this.disciline = res.data;
        });
    },
    handleEdit(row) {
      this.updataName = row.major;
      this.updataId = row.id;
      this.dialogFormVisible = true;
    },
    handleDel(row) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteMajors.action',
        params: {
          id: row.id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('删除成功');
            this.get();
          } else {
            this.$message.error('删除失败');
          }
        })
        .catch(() => {
          this.$message.error('删除出错');
        });
    },
    // 新增学科
    add() {
      if (this.addName !== '') {
        this.$http({
          method: 'POST',
          url: '/pjsys/addMajors.action',
          params: {
            major: this.addName,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('新增成功');
              this.addName = '';
              this.get();
            } else {
              this.$message.error('新增失败');
            }
          });
      } else {
        this.$message.error('新增学科不能为空');
      }
    },
    // 更新学科
    updata() {
      this.$http({
        method: 'POST',
        url: '/pjsys/updateMajors.action',
        params: {
          id: this.updataId,
          major: this.updataName,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.get();
            this.dialogFormVisible = false;
            this.$message.success('更新成功');
          }
        })
        .catch(() => {
          this.$message.error('更新失败');
        });
    },
  },

};
</script>

