<template>
  <div>
    <el-row>
      <el-col>
        <el-button
          type="primary"
          @click="back"
        >
          返回
        </el-button>
      </el-col>
    </el-row>
    <el-row
      :gutter="20"
      class="mt20"
    >
      <el-col
        :span="6"
        v-for="item of HaveClass"
        :key="item.id"
        style="width:300px"
      >
        <el-card class="mt20">
          <div
            slot="header"
          >
            <span>已配置班级</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
              @click="delClass(item.id)"
            >
              删除班级
            </el-button>
          </div>
          <div>
            {{ item.classname }}
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      HaveClass: [],
    };
  },
  mounted() {
    this.getHaveClass();
  },
  methods: {
    // 获取已有班级
    getHaveClass() {
      this.$http({
        method: 'GET',
        url: '/pjsys/classinfoInheadmaster_idIsTrue',
        params: {
          id: this.$route.params.id,
        },
      })
        .then((res) => {
          this.HaveClass = res.data;
        });
    },
    // 删除班级
    delClass(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/classinfoClearheadmaster_id',
        params: {
          classinfo_id: id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功删除');
            this.getHaveClass();
          } else {
            this.$message.error('删除失败,请稍后重试');
          }
        });
    },
    back() {
      this.$router.push('/admin/tteacher');
    },
  },
};
</script>

<style scoped>
.mt20{
  margin-top: 20px;
}
</style>
