<template>
  <div>
    <el-button
      @click="search"
      type="primary"
    >
      按照学生姓名审核
    </el-button>
    <m-stu-table
      v-loading="loading"
      :stu="stu"
      @getStu="getStu"
    />
    <div style="float:right;margin-top:20px">
      <el-pagination
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import MStuTable from './Stu.vue';

export default {
  components: {
    MStuTable,
  },
  data() {
    return {
      stu: [],
      pages: 0,
      loading: true,
    };
  },
  created() {
    this.getStu();
  },
  methods: {
    getStu(page = 1) {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllInfo4.action',
        params: {
          page,
        },
      })
        .then((res) => {
          this.stu = res.data.datas;
          this.pages = res.data.pages;
          this.loading = false;
        });
    },
    handleCurrentChange(page) {
      this.getStu(page);
    },
    search() {
      this.$router.push('/admin/searchpayment');
    },
  },

};
</script>

