<template>
  <div>
    <el-row>
      <el-col :span="7">
        <p>当前班级:{{ this.$route.params.name }}   已配置人数:{{ num }}</p>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="addStu"
        >
          添加学生
        </el-button>
      </el-col>
      <!-- <el-col :span="3">
        <el-button
          type="primary"
          @click="reverse"
        >
          倒序查看
        </el-button>
      </el-col> -->
      <el-col :span="3">
        <el-button
          type="primary"
          @click="exportStudent"
        >
          导出学生
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="exportTemplate"
        >
          导出模板
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-upload
          action="/loadStudentExcel"
          :on-success="upSucess"
          :on-error="upError"
          :show-file-list="false"
        >
          <el-button
            type="primary"
          >
            导入学生
          </el-button>
        </el-upload>
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="clearStu"
        >
          清空
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          @click="back"
        >
          返回
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: '',
      id: '',
      false: false,
    };
  },
  mounted() {
    this.getNum();
    this.id = this.$route.params.id;
  },
  methods: {
    getNum() {
      this.$http({
        method: 'GET',
        url: '/pjsys/CountStudentByClassInfoId.action',
        params: {
          classinfo_id: this.$route.params.id,
        },
      })
        .then((res) => {
          this.num = res.data;
        });
    },
    addStu() {
      this.$router.push(`/admin/tclasssearchstu/${this.id}`);
    },
    reverse() {
      //
      this.$message.error('暂未开放');
    },
    exportStudent() {
      window.location.href = `download.action?classinfo_id=${this.id}`;
    },
    exportTemplate() {
      window.location.href = 'downloadStudent';
    },
    upSucess() {
      this.$message.success('成功');
    },
    upError() {
      this.$message.error('失败');
    },
    clearStu() {
      //
      this.$http({
        method: 'POST',
        url: '/pjsys/classinfoClearStudent',
        params: {
          classinfo_id: this.id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getStudent');
          } else {
            this.$message.error('失败');
          }
        });
    },
    // 返回
    back() {
      this.$router.push('/admin/tclass');
    },
  },
};
</script>

