<template>
  <div>
    <el-row style="margin-bottom:20px">
      <el-col :span="6">
        <el-input
          v-model="name"
          placeholder="请输入学生姓名"
          @keyup.enter.native="getList"
        />
      </el-col>
      <el-col :span="12">
        <el-button
          type="primary"
          @click="getList"
        >
          搜索
        </el-button>
      </el-col>
    </el-row>
    <el-table
      :data="TeacherList"
      class="table"
      v-loading="loading"
    >
      <el-table-column
        fixed
        :show-overflow-tooltip="true"
        align="center"
        prop="attence_time"
        label="考勤日期"
        width="120"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        prop="student.stuName"
        label="姓名"
        align="center"
        width="100"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="性别"
        width="80"
      >
        <template scope="scope">
          <span v-if="scope.row.student.sex ===1">
            男
          </span>
          <span v-if="scope.row.student.sex ===2">
            女
          </span>
        </template>
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        prop="student.personalNum"
        label="身份证号"
        align="center"
        width="170"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        prop="classinfo.classname"
        label="班级"
        align="center"
        width="120"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        label="考勤状态"
        align="center"
        width="100"
      >
        <template scope="scope">
          <div>
            {{ scope.row.attence_status === 1 ? '正常' : '缺勤' }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        label="缺勤原因"
        align="center"
        width="100"
      >
        <template scope="scope">
          <div>
            {{ scope.row.reasons === null ? '正常' : scope.row.reasons.reason }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            正常
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id)"
          >
            缺勤
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="请选择缺勤原因"
      width="400px"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form">
        <el-form-item
          label="缺勤原因"
          label-width="100px"
        >
          <el-select
            v-model="form.region"
            placeholder="请选择缺勤原因"
          >
            <el-option
              v-for="item in form.roleOption"
              :key="item.value"
              :label="item.reason"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="Updatasubmit"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      sel: '',
      loading: false,
      TeacherList: [],
      dialogFormVisible: false,
      id: '',
      form: {
        region: '',
        roleOption: [],
      },
    };
  },
  methods: {
    getList() {
      if (this.name) {
        this.$http({
          method: 'POST',
          url: '/pjsys/showAttence.action',
          params: {
            stuName: this.name,
          },
        })
          .then((res) => {
            if (res.data !== null) {
              this.TeacherList = res.data;
            } else {
              this.$message.error('搜索不到该学生信息');
            }
          });
      } else {
        this.$message({
          message: '请输入学生姓名',
          type: 'error',
        });
      }
    },
    // 改为正常
    handleEdit(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/updateAttence.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '更改成功',
              type: 'success',
            });
            this.getList();
          } else {
            this.$message({
              message: '更改失败',
              type: 'error',
            });
          }
        });
    },
    // 点击缺勤按钮 ,获取缺勤原因
    handleDelete(id) {
      this.id = id;
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllReasons.action',
      })
        .then((res) => {
          this.form.roleOption = res.data;
          this.dialogFormVisible = true;
        });
    },
    // 确定更改考勤
    Updatasubmit() {
      if (this.form.region !== '') {
        this.$http({
          method: 'POST',
          url: '/pjsys/attenceUpdateStatu.action',
          params: {
            id: this.id,
            attence_status: 2,
            reason_id: this.form.region,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message({
                message: '更改成功',
                type: 'success',
              });
              this.getList();
              this.dialogFormVisible = false;
              this.form.region = '';
            } else {
              this.$message({
                message: '更改失败',
                type: 'error',
              });
            }
          });
      }
    },
  },
};
</script>

