<template>
  <div>
    <m-header @getInfo="getInfo" />
    <m-table
      :info="info"
      @getInfo="getInfo"
    />
  </div>
</template>

<script>
import MHeader from './SelHeader.vue';
import MTable from './SelTable.vue';

export default {
  components: {
    MHeader,
    MTable,
  },
  data() {
    return {
      info: [],
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      this.$http({
        method: 'GET',
        url: '/findInfoById.action',
        params: {
          stu_id: this.$route.params.id,
        },
      })
        .then((res) => {
          this.info = res.data;
        });
    },
  },
};
</script>

