<template>
  <div class="loginWrapper">
    <div class="loginContent">
      <div class="loginTitle">
        <img
          :src="require('assets/img/login-left.png')"
          alt="登录标题"
        >
        <span class="loginTitleText">
          登录
        </span>
        <img
          :src="require('assets/img/login-right.png')"
          alt="登录标题"
        >
      </div>
      <div class="loginInput">
        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
        >
          <el-form-item
            prop="name"
          >
            <el-input
              v-model="ruleForm2.name"
              class="inputWidth"
              placeholder="请输入账号"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item
            prop="pass"
          >
            <el-input
              type="password"
              v-model="ruleForm2.pass"
              class="inputWidth"
              placeholder="请输入密码"
              @keyup.enter.native="submitForm('ruleForm2')"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item>
            <el-row>
              <el-col :span="5">
                <el-radio
                  v-model="ruleForm2.role"
                  :label="1"
                >
                  教务
                </el-radio>
              </el-col>
              <el-col :span="5">
                <el-radio
                  :label="2"
                  v-model="ruleForm2.role"
                >
                  财务
                </el-radio>
              </el-col>
              <el-col :span="7">
                <el-radio
                  :label="3"
                  v-model="ruleForm2.role"
                >
                  班主任
                </el-radio>
              </el-col>
              <el-col :span="7">
                <el-radio
                  :label="4"
                  v-model="ruleForm2.role"
                  size="mini"
                >
                  管理员
                </el-radio>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item>
            <el-button
              class="submit"
              type="primary"
              @click="submitForm('ruleForm2')"
            >
              登录
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      }
      callback();
    };
    const validateName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账号'));
      }
      callback();
    };
    return {
      ruleForm2: {
        pass: '',
        name: '',
        role: 1,
      },
      rules2: {
        pass: [
          { validator: validatePass, trigger: 'blur' },
        ],
        name: [
          { validator: validateName, trigger: 'blur' },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      // eslint-disable-next-line consistent-return
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http({
            methods: 'POST',
            url: '/pjsys/teacherlogin.action',
            params: {
              username: this.ruleForm2.name,
              password: this.ruleForm2.pass,
              role: this.ruleForm2.role,
            },
          })
            .then((res) => {
              if (res.data) {
                this.$message.success('成功');
                this.$Storage.set('teacherID', res.data.id);
                this.$Storage.set('role', res.data.role);
                this.$Storage.set('teacherName', res.data.username);
                this.$router.push('/admin');
              } else {
                this.$message.success('账号或密码错误');
              }
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

