<template>
  <div>
    <m-header @getClassInfo="getClassInfo">
      <el-col :span="3">
        <el-input
          placeholder="请输入班级名"
          v-model="className"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="all"
        >
          全部班级
        </el-button>
      </el-col>
    </m-header>
    <m-table
      :info="info"
      @getClassInfo="getClassInfo"
      v-loading="loading"
    />
    <el-pagination
      style="margin-top:20px;float:right"
      background
      layout="prev, pager, next,jumper"
      :total="total"
      @current-change="page"
      :current-page="pages"
    />
  </div>
</template>

<script>
import MTable from './Table.vue';
import MHeader from './Header.vue';

export default {
  components: {
    MTable,
    MHeader,
  },
  data() {
    return {
      info: [],
      className: '',
      total: 0,
      pages: 1,
      loading: true,
    };
  },
  mounted() {
    this.getClassInfo();
  },
  methods: {
    getClassInfo() {
      //
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassinfo.action',
        params: {
          page: this.pages,
        },
      })
        .then((res) => {
          this.info = res.data.datas;
          this.total = res.data.total;
          this.loading = false;
        });
    },
    // 换页
    page(page) {
      this.loading = true;
      this.pages = page;
      this.getClassInfo();
    },
    search() {
      if (this.className === '') {
        this.$message.error('请输入要查找的班级名');
      } else {
        this.$http({
          method: 'GET',
          url: '/pjsys/showclassinfo.action',
          params: {
            classname: this.className,
          },
        })
          .then((res) => {
            this.info = res.data;
            this.total = 0;
          });
      }
    },
    all() {
      this.pages = 1;
      this.getClassInfo();
    },
  },
};
</script>

