<template>
  <el-form label-width="80px">
    <el-form-item label="标题">
      <el-input
        v-model="header"
        style="width:300px"
        placeholder="请输入标题,十个字符以内"
      />
    </el-form-item>
    <el-form-item label="内容">
      <el-input
        v-model="content"
        type="textarea"
        style="width:300px"
        placeholder="请输入内容"
      />
    </el-form-item>
    <el-form-item>
      <el-button
        type="primary"
        @click="send"
        style="width:300px"
      >
        发送
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      content: '',
      header: '',
      teacherID: '',
    };
  },
  mounted() {
    this.teacherID = this.$Storage.get('teacherID');
    if (this.teacherID === '' || this.teacherID === undefined) {
      this.$message.error('没有登录,请登录');
      this.$router.push('/adminlogin');
    }
  },
  methods: {
    send() {
      if (this.content === '' || this.header === '') {
        this.$message.error('不允许为空');
      } else {
        this.sendNotice();
      }
    },
    sendNotice() {
      const data = this.$qs.stringify({
        teacher_id: this.teacherID,
        'imessageByInter.head': this.header,
        'imessageByInter.message': this.content,
      });
      this.$http({
        method: 'POST',
        url: '/pjsys/addImessageByInter.action',
        data,
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功发送');
          } else {
            this.$message.error('发送失败');
          }
        });
    },
  },

};
</script>

