<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="3">
        <el-button
          type="primary"
          @click="add"
        >
          新增科目
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button
          type="primary"
          @click="addInter"
        >
          新增面试场次
        </el-button>
      </el-col>
    </el-row>
    <el-dialog
      title="新增科目"
      :visible.sync="dialogFormVisible"
      width="500px"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="学科">
          <el-select
            v-model="value"
            filterable
            placeholder="请选择学科"
          >
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.major"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="科目名">
          <el-input
            v-model="subjectName"
            placeholder="请输入科目名"
            style="width:60%"
          />
        </el-form-item>
        <el-form-item label="科目简介">
          <el-input
            type="textarea"
            v-model="introduction"
            placeholder="请输入简介"
            style="width:60%"
          />
        </el-form-item>
        <el-form-item label="是否面试">
          <el-radio
            label="1"
            v-model="radio"
          >
            是
          </el-radio>
          <el-radio
            label="0"
            v-model="radio"
          >
            否
          </el-radio>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="submit"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="新增科目"
      :visible.sync="dialogInter"
      width="600px"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="面试科目">
          <el-select
            v-model="InterSubject"
            filterable
            placeholder="请选择面试科目"
          >
            <el-option
              v-for="item in Interoptions"
              :key="item.id"
              :label="item.interviewSubject"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="面试时间">
          <el-date-picker
            v-model="InterDate"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-DD+HH:MM"
          />
        </el-form-item>
        <el-form-item label="面试人数">
          <el-input
            v-model="interNum"
            placeholder="请输入面试人数"
            style="width:400px"
          />
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogInter = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="interSubmit"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      dialogInter: false,
      subjectName: '',
      value: '',
      options: [],
      radio: '1',
      introduction: '',
      InterSubject: '',
      Interoptions: [],
      InterDate: '',
      interNum: '',
    };
  },
  mounted() {
    this.getDiscip();
  },
  methods: {
    getDiscip() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllMajors.action',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    add() {
      this.dialogFormVisible = true;
    },
    submit() {
      this.$http({
        method: 'POST',
        url: '/pjsys/addSubject.action',
        params: {
          major_id: this.value,
          subject: this.subjectName,
          introduction: this.introduction,
          interview: this.radio,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$emit('getSubject');
            this.$message.success('新建成功');
            this.dialogFormVisible = false;
          } else {
            this.$message.error('新建失败');
          }
        });
    },
    getInterOptions() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllInterview.action',
      })
        .then((res) => {
          this.Interoptions = res.data;
        });
    },
    //
    addInter() {
      // 面试时间显示
      this.dialogInter = true;
      this.getInterOptions();
    },
    interSubmit() {
      if (this.InterSubject === ''
      || this.interNum === ''
      || this.InterDate === ''
      ) {
        this.$message.error('不允许为空');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/addInterview_timeslot.action',
          params: {
            interview_id: this.InterSubject,
            starttime: this.InterDate[0],
            end_time: this.InterDate[1],
            num: this.interNum,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.dialogInter = false;
              this.$message.success('成功');
              this.InterSubject = '';
              this.interNum = '';
              this.InterDate = '';
            } else {
              this.$message.error('失败');
            }
          });
      }
    },
  },
};
</script>

