<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="3">
        <el-input
          placeholder="请输入内容"
          v-model="name"
          :disabled="true"
        />
      </el-col>
      <el-col :span="4">
        <el-select
          v-model="value"
          placeholder="请选择班级"
        >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.classname"
            :value="item.id"
          />
        </el-select>
      </el-col>
    </el-row>
    <el-row style="margin-top:20px">
      <el-col :span="2">
        <el-button
          @click="back"
        >
          返回
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="add"
        >
          确定
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options: [],
      value: '',
      name: '',
    };
  },
  mounted() {
    this.getClass();
    this.name = this.$route.params.name;
  },
  methods: {
    getClass() {
      this.$http({
        method: 'GET',
        url: '/pjsys/classinfoInheadmaster_idIsNull',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    add() {
      if (this.value === '' || this.name === '') {
        this.$message.error('请选择班级');
      } else {
        this.addClass();
      }
    },
    addClass() {
      this.$http({
        method: 'POST',
        url: '/pjsys/classinfoUpdateheadmaster_id',
        params: {
          id: this.$route.params.id,
          classinfo_id: this.value,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$router.push('/admin/tteacher');
          } else {
            this.$message.error('失败');
          }
        });
    },
    back() {
      this.$router.push('/admin/tteacher');
    },
  },
};
</script>

