<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="4">
        <el-select
          v-model="classInfo"
          placeholder="请选择班级"
          @change="change"
        >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.interviewSubject"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-button @click="back">
          返回
        </el-button>
      </el-col>
    </el-row>
    <m-table
      :interview="interview"
      @getInter="getInter"
    />
    <el-pagination
      style="margin-top:20px;float:right"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="page"
    />
  </div>
</template>

<script>
import MTable from './Table.vue';

export default {
  components: {
    MTable,
  },
  data() {
    return {
      interview: [],
      total: 0,
      name: '',
      classInfo: '',
      options: [],
      id: '',
    };
  },
  mounted() {
    this.getInter();
    this.getClassInfo();
  },
  methods: {
    page(page) {
      this.getInter(page);
    },
    getInter(page = 1) {
      this.$http({
        method: 'GET',
        url: '/pjsys/findByInterview.action',
        params: {
          interview_id: this.id,
          page,
        },
      })
        .then((res) => {
          this.interview = res.data.datas;
          this.total = res.data.total;
        });
    },
    getClassInfo() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllInterview.action',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    // findByInterview.action
    change(event) {
      this.id = event;
      this.getInter();
    },
    back() {
      this.$router.push('/admin/tinterviews');
    },
  },
};
</script>

