<template>
  <div>
    <el-row
      style="margin-bottom:20px"
    >
      <el-col :span="6">
        <el-input
          v-model="subject"
          placeholder="请输入科目名"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          查找
        </el-button>
      </el-col>
    </el-row>
    <el-table
      :data="student"
      style="width: 100%"
    >
      <el-table-column
        prop="0"
        label="科目名称"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="缴费人数"
      >
        <template scope="scope">
          <span v-if="scope.row[2] === null">
            暂无
          </span>
          <span v-else>
            {{ scope.row[2] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="收入金额"
      >
        <template scope="scope">
          <span v-if="scope.row[1] === null">
            暂无
          </span>
          <span v-else>
            {{ scope.row[1] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column

        label="退款人数"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template scope="scope">
          <span v-if="scope.row[3] === null">
            暂无
          </span>
          <span v-else>
            {{ scope.row[3] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        label="退款金额"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template scope="scope">
          <span v-if="scope.row[4] === null">
            暂无
          </span>
          <span v-else>
            {{ scope.row[4] }}
          </span>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top:20px">
      <el-pagination
        style="float:right"
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      subject: '',
      student: [],
      pages: 0,
    };
  },
  mounted() {
    this.getStu();
  },
  methods: {
    getStu(page = 1, subject) {
      this.$http({
        method: 'POST',
        url: '/pjsys/find1.action',
        params: {
          page,
          subject,
        },
      })
        .then((res) => {
          this.student = res.data.datas;
          this.pages = res.data.pages;
        })
        .catch(() => {
          this.$message.error('出错啦!');
        });
    },
    handleCurrentChange(page) {
      this.getStu(page);
    },
    search() {
      if (this.subject === '') {
        this.getStu();
      } else {
        this.getStu(1, this.subject);
      }
    },
  },
};
</script>

