<template>
  <div>
    <el-row>
      <el-col :span="4">
        <el-input
          v-model="name"
          placeholder="请输入学生姓名"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          @click="search"
          type="primary"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          @click="all"
          type="primary"
        >
          审核全部
        </el-button>
      </el-col>
    </el-row>
    <m-stu-table
      v-loading="loading"
      :stu="stu"
      @getStu="getStu"
    />
  </div>
</template>

<script>
import MStuTable from './Stu.vue';

export default {
  components: {
    MStuTable,
  },
  data() {
    return {
      stu: [],
      name: '',
      loading: true,
    };
  },
  created() {
    this.getStu();
  },
  methods: {
    search() {
      if (this.name !== '') {
        this.getStu();
      } else {
        this.$message.error('请输入学生姓名');
      }
    },
    getStu() {
      if (this.name !== '') {
        this.$http({
          method: 'POST',
          url: '/pjsys/show4.action',
          params: {
            stuName: this.name,
          },
        })
          .then((res) => {
            this.stu = res.data;
            this.loading = false;
          });
      } else {
        this.loading = false;
      }
    },
    all() {
      this.$router.push('/admin/payment');
    },
  },

};
</script>

