<template>
  <el-form
    :model="form"
    label-width="80px"
  >
    <el-form-item label="教室">
      <el-select
        v-model="form.classRoom"
        placeholder="请选择教室"
      >
        <el-option
          v-for="item in form.classRoomOptions"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="星期">
      <el-select
        v-model="form.date"
        placeholder="请选择星期几"
      >
        <el-option
          v-for="item in form.dateOptions"
          :key="item.key"
          :label="item.value"
          :value="item.key"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="时间">
      <el-time-picker
        is-range
        v-model="form.time"
        range-separator="至"
        start-placeholder="开始时间"
        end-placeholder="结束时间"
        placeholder="选择时间范围"
        format="HH:mm"
        value-format="HH:mm"
      />
    </el-form-item>
    <el-form-item>
      <el-button
        @click="back"
        style="width:200px"
      >
        返回
      </el-button>
      <el-button
        @click="submit"
        type="primary"
        style="width:200px"
      >
        新增
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        classRoom: '',
        classRoomOptions: [],
        time: '',
        date: '',
        dateOptions: [
          {
            key: 1,
            value: '星期一',
          },
          {
            key: 2,
            value: '星期二',
          },
          {
            key: 3,
            value: '星期三',
          },
          {
            key: 4,
            value: '星期四',
          },
          {
            key: 5,
            value: '星期五',
          },
          {
            key: 6,
            value: '星期六',
          },
          {
            key: 7,
            value: '星期日',
          },
        ],
      },
    };
  },
  mounted() {
    this.getRoom();
  },
  methods: {
    getRoom() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassroom.action',
      })
        .then((res) => {
          this.form.classRoomOptions = res.data;
        });
    },
    back() {
      this.$router.push('/admin/tclassroom');
    },
    submit() {
      if (
        this.form.classRoom === ''
        || this.form.time === ''
        || this.form.date === ''
      ) {
        this.$message.error('不允许为空');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/addClassroominfo.action',
          params: {
            classroom_id: this.form.classRoom,
            day: this.form.date,
            start: this.form.time[0],
            jieshu: this.form.time[1],
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('成功');
            } else {
              this.$message.error('失败');
            }
          });
      }
    },
  },
};
</script>

