<template>
  <el-form
    ref="form"
    :model="form"
    label-width="120px"
    style="width:500px"
  >
    <el-form-item label="姓名">
      <el-input
        v-model="form.name"
        placeholder="请输入班主任姓名"
      />
    </el-form-item>
    <el-form-item
      label="身份证号"
    >
      <el-input
        v-model="form.card"
        placeholder="请输入班主任身份证号"
      />
    </el-form-item>
    <el-form-item label="联系方式">
      <el-input
        v-model="form.tel"
        placeholder="请输入班主任联系方式"
      />
    </el-form-item>
    <el-form-item label="备注">
      <el-input
        type="textarea"
        placeholder="请输入班主任备注"
        v-model="form.desc"
      />
    </el-form-item>
    <el-form-item label="性别">
      <el-radio
        v-model="form.sex"
        label="男"
      />
      <el-radio
        v-model="form.sex"
        label="女"
      />
    </el-form-item>
    <el-form-item>
      <el-button
        type="primary"
        @click="onSubmit"
      >
        立即创建
      </el-button>
      <el-button @click="back">
        取消返回
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '',
        desc: '',
        sex: '男',
        tel: '',
        card: '',
      },
    };
  },
  methods: {
    back() {
      this.$router.push('/admin/tteacher');
    },
    onSubmit() {
      if (
        this.form.name === ''
        || this.form.tel === ''
        || this.form.card === '') {
        this.$message.error('不允许为空');
      } else {
        this.add();
      }
    },
    add() {
      this.$http({
        method: 'POST',
        url: '/pjsys/addHeadMaster',
        params: {
          name: this.form.name,
          sex: this.form.sex,
          phonenumber: this.form.tel,
          card: this.form.card,
          comment: this.form.desc,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$router.push('/admin/tteacher');
          } else {
            this.$message.success('失败');
          }
        });
    },
  },
};
</script>

