<template>
  <div>
    <el-table
      :data="subject"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label="科目"
        prop="subject"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="简介"
        prop="introduction"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="面试"
        :show-overflow-tooltip="true"
        align="center"
        width="80"
      >
        <template slot-scope="scope">
          {{ scope.row.interview === 0? '否':'是' }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"
          >
            修改
          </el-button>
          <el-button
            v-if="scope.row.interview === 1"
            size="mini"
            type="primary"
            @click="handleDetail(scope.row.interview_id)"
          >
            场次详情
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="面试场次详情"
      :visible.sync="dialogDeatil"
    >
      <el-table
        :data="detail"
        style="width: 100%;margin-top:20px"
      >
        <el-table-column
          label="开始时间"
          prop="starttime"
          :show-overflow-tooltip="true"
          align="center"
        />
        <el-table-column
          prop="end_time"
          label="结束时间"
          :show-overflow-tooltip="true"
          align="center"
        />
        <el-table-column
          prop="num"
          label="人数"
          :show-overflow-tooltip="true"
          align="center"
        />
        <el-table-column
          label="操作"
          align="center"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleDetailEdit(scope.row)"
            >
              修改
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogDeatil = false">
          取 消
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="面试场次修改"
      :visible.sync="dialogEditDeatil"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="面试科目">
          <el-input
            v-model="InterSubject"
            style="width:400px"
            disabled
            placeholder="面试科目"
          />
        </el-form-item>
        <el-form-item label="面试时间">
          <el-date-picker
            v-model="InterDate"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-DD+HH:MM"
          />
        </el-form-item>
        <el-form-item label="面试人数">
          <el-input
            v-model="interNum"
            placeholder="请输入面试人数"
            style="width:400px"
          />
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="EditDeatil"
        >
          确定
        </el-button>
        <el-button @click="dialogEditDeatil = false">
          取 消
        </el-button>
      </div>
    </el-dialog>
    <!-- 修改科目 -->
    <el-dialog
      title="修改科目"
      :visible.sync="dialogUpdata"
      width="600px"
    >
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="面试科目">
          <el-select
            v-model="UpdataSubject"
            filterable
            placeholder="请选择面试科目"
          >
            <el-option
              v-for="item in Updataoptions"
              :key="item.id"
              :label="item.major"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="科目名">
          <el-input
            style="width:400px"
            v-model="UpdataSubjectName"
            placeholder="请输入科目名"
          />
        </el-form-item>
        <el-form-item label="科目简介">
          <el-input
            type="textarea"
            v-model="UpdataIntroduction"
            placeholder="请输入科目简介"
            style="width:400px"
          />
        </el-form-item>
        <el-form-item label="是否面试">
          <el-radio
            label="1"
            v-model="Updataradio"
          >
            是
          </el-radio>
          <el-radio
            label="0"
            v-model="Updataradio"
          >
            否
          </el-radio>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogUpdata = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="UpdataSubmit"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    subject: [String, Array],
  },
  data() {
    return {
      detail: [],
      detailId: '',
      dialogDeatil: false,
      dialogEditDeatil: false,
      dialogUpdata: false,
      interNum: '',
      InterDate: '',
      InterSubject: '',
      InterId: '',
      UpdataSubject: '',
      Updataoptions: [],
      UpdataSubjectName: '',
      UpdataIntroduction: '',
      Updataradio: '1',
      UpdaId: '',
    };
  },
  mounted() {
  },
  methods: {
    // 修该
    handleEdit(row) {
      this.dialogUpdata = true;
      this.getUpdataoptions();
      this.UpdataSubject = row.majors.id;
      this.UpdataSubjectName = row.subject;
      this.UpdaId = row.id;
      this.Updataradio = String(row.interview);
      this.UpdataIntroduction = row.introduction;
    },
    // 获取学科列表
    getUpdataoptions() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllMajors.action',
      })
        .then((res) => {
          this.Updataoptions = res.data;
        });
    },
    // 确认修改
    UpdataSubmit() {
      if (
        this.UpdataSubject === ''
        || this.UpdataSubjectName === ''
        || this.UpdataIntroduction === ''
        || this.Updataradio === ''
      ) {
        this.$message.error('不允许为空');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/updateSubject.action',
          params: {
            id: this.UpdaId,
            major_id: this.UpdataSubject,
            subject: this.UpdataSubjectName,
            introduction: this.UpdataIntroduction,
            interview: this.Updataradio,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.dialogUpdata = false;
              this.$message.success('更新成功');
              this.$emit('getSubject');
            } else {
              this.$message.error('更新失败');
            }
          });
      }
    },
    // 删除
    handleDel(row) {
      this.$http({
        method: 'POST',
        url: '/pjsys/pisys/deleteSubject.action',
        params: {
          id: row.id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功删除');
            this.$emit('getSubject');
          } else {
            this.$message.error('删除失败');
          }
        });
    },
    // 场次详情
    handleDetail(id) {
      this.detailId = id;
      this.dialogDeatil = true;
      this.$http({
        method: 'GET',
        url: '/pjsys/queryInterviewtimeslotActionByinterviewid',
        params: {
          interview_id: id,
        },
      })
        .then((res) => {
          this.detail = res.data;
        });
    },
    // 修改场次
    handleDetailEdit(row) {
      this.dialogEditDeatil = true;
      this.interNum = row.num;
      this.InterId = row.id;
      this.InterDate = `${row.starttime}e-${row.end_time}`;
      this.InterSubject = row.interview.interviewSubject;
    },
    // 确认更改场次
    EditDeatil() {
      if (this.InterDate === ''
      || this.InterId === ''
      || this.InterSubject === ''
      || this.interNum === ''
      ) {
        this.$message.error('不允许为空');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/updateInterviewSubject.action',
          params: {
            interview_timeslot_id: this.InterId,
            num: this.interNum,
            starttime: this.InterDate[0],
            end_time: this.InterDate[1],
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('成功更新');
              this.handleDetail(this.detailId);
              this.InterDate = '';
              this.InterId = '';
              this.InterSubject = '';
              this.interNum = '';
              this.dialogEditDeatil = false;
            } else {
              this.$message.error('更新失败');
            }
          });
      }
    },
  },
};
</script>

