<template>
  <div>
    <el-table
      :data="student"
      style="width: 100%"
    >
      <el-table-column
        prop="classname"
        label="班级"
        width="150"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="count"
        label="应到人数"
        align="center"
      />
      <el-table-column
        prop="onPerson"
        :show-overflow-tooltip="true"
        align="center"
        label="正常人数"
      />
      <el-table-column
        prop="unPerson"
        :show-overflow-tooltip="true"
        align="center"
        label="缺勤人数"
      />
      <el-table-column
        prop="notPerson"
        :show-overflow-tooltip="true"
        align="center"
        label="未考勤人数"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="具体缺勤学生"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            具体缺勤学生
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="具体缺勤学生"
      :visible.sync="dialogTableVisible"
    >
      <el-table :data="gridData">
        <el-table-column
          property="student.stuName"
          label="学生"
        />
        <el-table-column
          property="reasons.reason"
          label="缺勤原因"
        />
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    student: [String, Array],
  },
  data() {
    return {
      dialogTableVisible: false,
      gridData: [],
    };
  },
  methods: {
    // 详细缺勤学生
    handleEdit(id) {
      this.$http({
        method: 'GET',
        url: '/pjsys/unAttenceMessage',
        params: {
          classinfo_id: id,
        },
      })
        .then((res) => {
          if (res.data.length > 0) {
            this.dialogTableVisible = true;
            this.gridData = res.data;
          } else {
            this.$message.error('当前班级没有缺勤学生');
          }
        })
        .catch(() => {
          this.$message.error('获取数据出错');
        });
    },
  },
};
</script>

