<template>
  <div>
    <el-form label-width="100px">
      <el-form-item label="科目:">
        <el-select
          v-model="subject"
          placeholder="请先选择科目"
          disabled
          style="width:300px"
        >
          <el-option
            v-for="item in subjectOptions"
            :key="item.id"
            :label="item.subject"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="班级名:">
        <el-input
          v-model="className"
          placeholder="请输入班级名"
          style="width:300px"
        />
      </el-form-item>
      <el-form-item label="报名费:">
        <el-input
          v-model="apply"
          placeholder="请输入报名费"
          style="width:300px"
          type="number"
        />
      </el-form-item>
      <el-form-item label="课时:">
        <el-input
          v-model="keshi"
          placeholder="请输入课时"
          style="width:300px"
          type="number"
        />
      </el-form-item>
      <el-form-item label="课时费:">
        <el-input
          v-model="money"
          placeholder="请输入课时费"
          style="width:300px"
          type="number"
        />
      </el-form-item>
      <el-form-item label="额定人数:">
        <el-input
          v-model="number"
          placeholder="请输入可报名额"
          style="width:300px"
          type="number"
        />
      </el-form-item>
      <el-form-item label="年龄上限:">
        <el-date-picker
          v-model="up"
          type="date"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          style="width:300px"
        />
      </el-form-item>
      <el-form-item label="年龄下限:">
        <el-date-picker
          v-model="down"
          type="date"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          style="width:300px"
        />
      </el-form-item>
      <el-form-item label="开放选课:">
        <el-radio
          v-model="kaifang"
          label="1"
        >
          开放
        </el-radio>
        <el-radio
          v-model="kaifang"
          label="0"
        >
          不开放
        </el-radio>
      </el-form-item>
      <el-form-item>
        <el-button
          @click="back"
          style="width:140px"
        >
          取消
        </el-button>
        <el-button
          type="primary"
          @click="add"
          style="width:140px"
        >
          更改
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      apply: '',
      className: '',
      keshi: '',
      money: '',
      subject: '',
      kaifang: '1',
      number: '',
      up: '',
      down: '',
      id: '',
      subjectOptions: [],
    };
  },
  mounted() {
    // this.getSubject();
    this.getInfo();
  },
  methods: {
    getInfo() {
      this.$http({
        method: 'GET',
        url: '/pjsys/showclassinfoId',
        params: {
          bid: this.$route.params.id,
        },
      })
        .then((res) => {
          this.className = res.data[0].classname;
          this.apply = res.data[0].unit_price;
          this.keshi = res.data[0].keshi;
          this.money = res.data[0].money;
          this.number = res.data[0].number;
          this.up = res.data[0].max_age;
          this.down = res.data[0].min_age;
          this.subject = res.data[0].subject.subject;
          this.id = res.data[0].id;
          this.kaifang = String(res.data[0].kaifang);
        });
    },
    getSubject() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllSubject.action',
      })
        .then((res) => {
          this.subjectOptions = res.data;
        });
    },
    add() {
      if (
        this.apply === ''
        || this.className === ''
        || this.keshi === ''
        || this.subject === ''
        || this.kaifang === ''
        || this.up === ''
        || this.down === ''
        || this.money === ''
        || this.number === ''
      ) {
        this.$message.error('不允许为空');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/updateClassinfo.action',
          params: {
            id: this.id,
            classname: this.className,
            unit_price: this.apply,
            max_age: this.up,
            min_age: this.down,
            keshi: this.keshi,
            money: this.money,
            number: this.number,
            kaifang: this.kaifang,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('成功');
              this.$router.push('/admin/tclass');
            } else {
              this.$message.error('失败');
            }
          });
      }
    },
    back() {
      this.$router.push('/admin/tclass');
    },
  },
};
</script>

