<template>
  <div class="loginWrapper">
    <div class="loginContent">
      <div class="loginTitle">
        <img
          :src="require('assets/img/login-left.png')"
          alt="重置密码"
        >
        <span class="loginTitleText">
          重置密码
        </span>
        <img
          :src="require('assets/img/login-right.png')"
          alt="重置密码"
        >
      </div>
      <div class="loginInput">
        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
        >
          <el-form-item
            prop="name"
          >
            <el-input
              v-model="ruleForm2.name"
              class="inputWidth"
              placeholder="请输入手机号"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item
            prop="pass"
          >
            <el-input
              type="password"
              v-model="ruleForm2.pass"
              class="inputWidth"
              placeholder="请输入密码"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item
            prop="checkPass"
          >
            <el-input
              type="password"
              v-model="ruleForm2.checkPass"
              class="inputWidth"
              placeholder="请再次输入密码"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item
            required
          >
            <el-row :gutter="20">
              <el-col :span="12">
                <el-form-item prop="yzm">
                  <el-input
                    v-model="ruleForm2.yzm"
                    class="inputWidth"
                    placeholder="请输入验证码"
                    autocomplete="off"
                  />
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item prop="date2">
                  <el-button
                    v-if="ruleForm2.show"
                    style="width: 100%"
                    type="primary"
                    @click="getyzm('ruleForm2')"
                  >
                    获取验证码
                  </el-button>
                  <el-button
                    v-else
                    style="width: 100%"
                    type="primary"
                  >
                    {{ ruleForm2.yzmVal }}s之后获取
                  </el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item>
            <el-button
              class="submit"
              type="primary"
              @click="submitForm('ruleForm2')"
            >
              重置密码
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="accountWrapper">
        <router-link
          class="accountContent"
          to="/login"
        >
          想起密码?立即登录
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else { callback(); }
    };
    const validateCheckPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    const validateName = (rule, value, callback) => {
      const myreg = /^[1][0-9]{10}$/;
      if (value === '') {
        callback(new Error('请输入账号'));
      } else if (!myreg.test(value)) {
        callback(new Error('手机号有误'));
      } else { callback(); }
    };
    const validateyzm = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'));
      }
      callback();
    };
    return {
      ruleForm2: {
        pass: '',
        name: '',
        yzm: '',
        checkPass: '',
        yzmVal: 60,
        show: true,
      },
      rules2: {
        pass: [
          { validator: validatePass, trigger: 'blur' },
        ],
        name: [
          { validator: validateName, trigger: 'blur' },
        ],
        checkPass: [
          { validator: validateCheckPass, trigger: 'blur' },
        ],
        yzm: [
          { validator: validateyzm, trigger: 'blur' },
        ],
      },
    };
  },
  methods: {
    getyzm() {
      const myreg = /^[1][0-9]{10}$/;
      if (!myreg.test(this.ruleForm2.name)) {
        this.$message({
          showClose: true,
          message: '手机号错误',
          type: 'error',
        });
      } else {
        this.ruleForm2.show = false;
        this.ruleForm2.yzmVal = 60;
        const timer = setInterval(() => {
          if (this.ruleForm2.yzmVal > 1 && this.ruleForm2.yzmVal <= 60) {
            this.ruleForm2.yzmVal -= 1;
          } else {
            clearInterval(timer);
            this.ruleForm2.show = true;
          }
        }, 1000);
        this.$http({
          methods: 'POST',
          url: '/pjsys/sendYzm.action',
          params: {
            mobilephone: this.ruleForm2.name,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message({
                message: '已发送验证码',
                type: 'success',
              });
            } else {
              this.$message({
                message: '手机号错误',
                type: 'error',
              });
            }
          })
          .catch(() => {
            this.$message({
              message: '登录错误',
              type: 'error',
            });
          });
      }
    },
    submitForm(formName) {
      // eslint-disable-next-line consistent-return
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http({
            methods: 'POST',
            url: '/pjsys/reset.action',
            params: {
              mobilephone: this.ruleForm2.name,
              password: this.ruleForm2.pass,
              yzm: this.ruleForm2.yzm,
            },
          })
            .then((res) => {
              if (res.data === 1) {
                this.$message({
                  message: '重置成功',
                  type: 'success',
                });
                this.$router.push('/login');
              } else if (res.data === 2) {
                this.$message({
                  message: '验证码错误',
                  type: 'error',
                });
              } else if (res.data === 3) {
                this.$message({
                  message: '此账号未注册',
                  type: 'error',
                });
              } else {
                this.$message({
                  message: '重置失败',
                  type: 'error',
                });
              }
            })
            .catch(() => {
              this.$message({
                message: '注册出现错误',
                type: 'error',
              });
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

