<template>
  <div>
    <el-button
      type="primary"
      class="btn"
      @click="exportAll"
    >
      导出全部
    </el-button>
    <el-date-picker
      v-model="data"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      value-format="yyyy-MM-dd"
    />
    <el-button
      type="primary"
      @click="exportTime"
    >
      按照时间
    </el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: '',
    };
  },
  methods: {
    exportAll() {
      window.location.href = 'downloadLogsAction?type=0';
    },
    exportTime() {
      window.location.href = `downloadLogsAction?type=1&startDate=${this.data[0]}&endDate=${this.data[1]}`;
    },
  },
};
</script>

<style scoped>
.btn{
  margin: 0 20px;
}
</style>
