<template>
  <div>
    <el-row
      style="margin-bottom:20px"
      :gutter="20"
    >
      <el-col :span="4">
        <el-select
          v-model="value"
          filterable
          placeholder="请选择班级"
          @change="search"
        >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.classname"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-button
          type="primary"
          @click="exportClass"
        >
          导出考勤数据
        </el-button>
      </el-col>
    </el-row>
    <el-table
      :data="student"
      style="width: 100%"
    >
      <el-table-column
        prop="0"
        label="姓名"
        width="100"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="1"
        label="班级"
        align="center"
      />
      <el-table-column
        prop="2"
        :show-overflow-tooltip="true"
        align="center"
        label="身份证号"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="考勤状态"
      >
        <template scope="scope">
          <span v-if="scope.row[3] ===1">
            正常
          </span>
          <span v-if="scope.row[3] ===2">
            缺勤
          </span>
        </template>
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="考勤原因"
      >
        <template scope="scope">
          {{ scope.row[4] === '' || scope.row[4] === null?'正常':scope.row[4] }}
        </template>
      </el-table-column>
    </el-table>
    <div style="float:right;margin-top:20px">
      <el-pagination
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: '',
      options: [],
      pages: 0,
      student: [],
    };
  },
  mounted() {
    this.getClass();
  },
  methods: {
    // 获取所有的班级
    getClass() {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllClassinfos.action',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    // 按班级显示
    search() {
      this.getStu();
    },
    getStu(page = 1) {
      this.$http({
        method: 'GET',
        url: '/pjsys/attendancesummary.action',
        params: {
          page,
          classinfo_id: this.value,
        },
      })
        .then((res) => {
          if (res.data.datas.length > 0) {
            this.student = res.data.datas;
            this.pages = res.data.pages;
          } else {
            this.student = [];
            this.$message.error('暂无考勤数据');
          }
        })
        .catch(() => {
          this.$message.error('数据加载戳错');
        });
    },
    // 换页
    handleCurrentChange(page) {
      this.getStu(page);
    },
    exportClass() {
      if (this.value === '') {
        this.$message.error('请选择班级进行导出');
      } else {
        window.location.href = `download4.action?classinfo_id=${this.value}`;
      }
    },
  },
};
</script>

