<template>
  <div>
    <el-row>
      <slot />
      <el-col :span="3">
        <el-button
          type="primary"
          @click="addClass"
        >
          新增班级
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="upClass"
        >
          升级班级
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="noRoom"
        >
          无教室班级
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="updateFlag"
        >
          更改状态
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="exportClass"
        >
          导出学生
        </el-button>
      </el-col>
    </el-row>
    <el-dialog
      title="升级班级提示"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <p>1.请先创建一个空的班级(升级过后的)</p>
      <p>2.请点击需要升级的班级后面的合并按钮</p>
      <p>3.选择刚刚创建好的升级班级,然后点击确定即可</p>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="没有教室的班级"
      :visible.sync="noRoomDia"
      width="30%"
    >
      <p>1.没有教室的班级,不会显示在下面表格中,</p>
      <p>2.此时最后一页数据就会暂时失效,此时请到教室管理下配置教室</p>
      <h3>下面是教室名</h3>
      <hr>
      <div
        v-for="item of noRoomClass"
        :key="item.id"
      >
        <p>{{ item.classname }}</p>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="noRoomDia = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="noRoomDia = false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      noRoomDia: false,
      noRoomClass: [],
    };
  },
  methods: {
    addClass() {
      this.$router.push('/admin/taddclass');
    },
    upClass() {
      this.dialogVisible = true;
    },
    noRoom() {
      this.noRoomDia = true;
      this.$http({
        method: 'GET',
        url: '/pjsys/classinfoInclassroominfo_idIsNull',
      })
        .then((res) => {
          this.noRoomClass = res.data;
        });
    },
    updateFlag() {
      this.$router.push('/admin/tclassflag');
    },
    exportClass() {

    },
  },
};
</script>

