<template>
  <div>
    <el-row>
      <el-col :span="5">
        <el-input
          v-model="name"
          @keyup.enter.native="addRoom"
          placeholder="请输入新增教室名"
        />
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="addRoom"
        >
          新增教室
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="addTime"
        >
          新增时间段
        </el-button>
      </el-col>
      <el-col :span="3">
        <el-button
          type="primary"
          @click="percent"
        >
          查看占有率
        </el-button>
      </el-col>
    </el-row>
    <el-dialog
      title="查看占有率"
      :visible.sync="dialogVisible"
      width="20%"
    >
      <el-form>
        <el-form-item label="教室">
          <el-select
            v-model="selclass"
            placeholder="请选择教室"
            @change="getPercent"
          >
            <el-option
              v-for="item in classOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="占有率">
          {{ selPercent }}
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      dialogVisible: false,
      selclass: '',
      classOptions: [],
      selPercent: '',
    };
  },
  mounted() {
    this.getRoom();
  },
  methods: {
    addRoom() {
      //
      if (this.name === '') {
        this.$message.error('请先输入教室名');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/addClassroom.action',
          params: {
            name: this.name,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('成功');
              this.$emit('getRoom');
            } else {
              this.$message.error('失败');
            }
          });
      }
    },
    percent() {
      //
      this.dialogVisible = true;
    },
    addTime() {
      //
      this.$router.push('/admin/troomtime');
    },
    getRoom() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassroom.action',
      })
        .then((res) => {
          this.classOptions = res.data;
        });
    },
    // 获取占有率
    getPercent() {
      this.$http({
        method: 'GET',
        url: '/pjsys/percent.action',
        params: {
          classroom_id: this.selclass,
        },
      })
        .then((res) => {
          this.selPercent = res.data;
        });
    },
  },
};
</script>

