<template>
  <div>
    <el-table
      :data="interview"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label="姓名"
        prop="student.stuName"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="student.personalNum"
        label="身份证号"
        :show-overflow-tooltip="true"
        align="center"
        width="160"
      />
      <el-table-column
        label="科目"
        prop="student.mobilephone"
        :show-overflow-tooltip="true"
        align="center"
        width="120"
      />
      <el-table-column
        label="面试结果"
        prop="result"
        :show-overflow-tooltip="true"
        align="center"
        width="140"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.result === 0">
            未审核
          </span>
          <span v-if="scope.row.result === 2">
            未通过
          </span>
          <span v-if="scope.row.result === 1">
            通过
          </span>
        </template>
      </el-table-column>
      <el-table-column
        label="申请时间"
        :show-overflow-tooltip="true"
        align="center"
        width="140"
      >
        <template slot-scope="scope">
          {{ scope.row.sqDt === null ? '暂无时间' : scope.row.sqDt }}
        </template>
      </el-table-column>
      <el-table-column
        label="审核时间"
        :show-overflow-tooltip="true"
        align="center"
        width="140"
      >
        <template slot-scope="scope">
          {{ scope.row.shDt === null ? '暂无时间' : scope.row.shDt }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handlePass(scope.row.student.id,scope.row.interview_timeslot.id)"
          >
            通过
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleUnpass(scope.row.student.id,scope.row.interview_timeslot.id)"
          >
            不通过
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    interview: [String, Array],
  },
  methods: {
    handlePass(id, interId) {
      this.$http({
        method: 'POST',
        url: '/pjsys/pass.action',
        params: {
          stu_id: id,
          interview_timeslot_id: interId,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getInter');
          } else {
            this.$message.error('失败');
          }
        });
    },
    handleUnpass(id, interId) {
      this.$http({
        method: 'POST',
        url: '/pjsys/unpass.action',
        params: {
          stu_id: id,
          interview_timeslot_id: interId,
        },
      })
        .then((res) => {
          if (res.data === 2) {
            this.$message.success('成功');
            this.$emit('getInter');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

