      <span>当前账户:</span>
      <span>{{ teacherName }}</span>
    </p>
    <el-button
      type="danger"
      @click="quit"
    >
      退出账户
    </el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      teacherName: '',
    };
  },
  mounted() {
    this.teacherName = this.$Storage.get('teacherName');
  },
  methods: {
