<template>
  <div>
    <el-row>
      <el-col :span="4">
        <el-input
          placeholder="请输入学生姓名"
          v-model="name"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button
          type="primary"
          @click="all"
        >
          全部学生
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="reverse"
        >
          倒序
        </el-button>
      </el-col>
    </el-row>
    <m-table
      :student="student"
      v-loading="loading"
    />
    <el-pagination
      style="margin-top:20px;float:right"
      background
      layout="prev, pager, next,jumper"
      :total="total"
      @current-change="page"
    />
  </div>
</template>

<script>
import MTable from './Table.vue';

export default {
  components: {
    MTable,
  },
  data() {
    return {
      student: [],
      total: 0,
      name: '',
      loading: true,
    };
  },
  mounted() {
    this.getAllStu();
  },
  methods: {
    search() {
      if (this.name === '') {
        this.$message.success('成功获取所有学生');
        this.getAllStu();
      } else {
        this.loading = true;
        this.$http({
          method: 'GET',
          url: '/show.action',
          params: {
            stuName: this.name,
          },
        })
          .then((res) => {
            this.student = res.data;
            this.total = res.data.total;
            this.loading = false;
          });
      }
    },
    reverse() {
      this.$http({
        method: 'GET',
        url: '/finddesc.action',
        params: {
          page: 1,
        },
      }).then((res) => {
        this.student = res.data.datas;
        this.total = res.data.total;
        this.loading = false;
      });
    },
    getAllStu(page = 1) {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllstudent.action',
        params: {
          page,
        },
      }).then((res) => {
        this.student = res.data.datas;
        this.total = res.data.total;
        this.loading = false;
      });
    },
    // 换页
    page(page) {
      this.loading = true;
      this.getAllStu(page);
    },
    all() {
      this.getAllStu();
    },
  },
};
</script>

