// troomhaveclass
<template>
  <div>
    <el-row>
      <el-col :span="3">
        <span>教室占有率:</span>
      </el-col>
      <el-col :span="4">
        {{ percent }}
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col
        :span="8"
        v-for="item of tableData"
        :key="item.classname"
        style="margin-top:20px"
      >
        <el-card>
          <div
            slot="header"
          >
            <span>{{ item.classname }}</span>
          </div>
          <div>
            <span v-if="item.classroominfo.day === 1">
              星期一
            </span>
            <span v-if="item.classroominfo.day === 2">
              星期二
            </span>
            <span v-if="item.classroominfo.day === 3">
              星期三
            </span>
            <span v-if="item.classroominfo.day === 4">
              星期四
            </span>
            <span v-if="item.classroominfo.day === 5">
              星期五
            </span>
            <span v-if="item.classroominfo.day === 6">
              星期六
            </span>
            <span v-if="item.classroominfo.day === 7">
              星期日
            </span>
            {{ item.classroominfo.start }}---{{ item.classroominfo.jieshu }}
          </div>
        </el-card>
      </el-col>
    </el-row>
    <el-row
      :gutter="20"
      style="margin-top:20px"
    >
      <el-col :span="4">
        <el-select
          v-model="selclass"
          placeholder="请选择班级"
        >
          <el-option
            v-for="item in classOptions"
            :key="item.id"
            :label="item.classname"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="6">
        <el-select
          v-model="seltime"
          placeholder="请选择时间段"
        >
          <el-option
            v-for="item in timeOptions"
            :key="item.id"
            :label="`星期${item.day}      ${item.start}---${item.jieshu}`"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="3">
        <el-button
          @click="submit"
          type="primary"
        >
          分配班级
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button @click="back">
          返回
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      classOptions: [],
      timeOptions: [],
      percent: '',
      selclass: '',
      seltime: '',
    };
  },
  mounted() {
    this.getHaveClass();
    this.getPercent();
    this.getClass();
    this.getTime();
  },
  methods: {
    // 获取已有班级
    getHaveClass() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findRoom.action',
        params: {
          classroom_id: this.$route.params.id,
        },
      })
        .then((res) => {
          this.tableData = res.data;
        });
    },
    // 获取占有率
    getPercent() {
      this.$http({
        method: 'GET',
        url: '/pjsys/percent.action',
        params: {
          classroom_id: this.$route.params.id,
        },
      })
        .then((res) => {
          this.percent = res.data;
        });
    },
    // 获取所有班级
    getClass() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAll1.action',
      })
        .then((res) => {
          this.classOptions = res.data;
        });
    },
    // 获取对应时间
    getTime() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAllClassroominfo.action',
        params: {
          classroom_id: this.$route.params.id,
        },
      })
        .then((res) => {
          this.timeOptions = res.data;
        });
    },
    // 分配教室
    submit() {
      if (this.selclass === '' || this.seltime === '') {
        this.$message.error('请选择班级或时间段');
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/selectClassroom.action',
          params: {
            id: this.selclass,
            'classroominfo.id': this.seltime,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('成功');
              this.getHaveClass();
              this.getPercent();
            } else {
              this.$message.error('失败');
            }
          });
      }
    },
    // 返回
    back() {
      this.$router.push('/admin/tclassroom');
    },
  },
};
</script>

