<template>
  <el-form
    :model="form"
    label-width="100px"
  >
    <el-form-item
      label="用户名"
    >
      <el-input
        style="width:300px"
        v-model="form.name"
        placeholder="请输入用户名/账号"
      />
    </el-form-item>
    <el-form-item
      label="密码"
    >
      <el-input
        style="width:300px"
        v-model="form.password"
        placeholder="请输入密码"
      />
    </el-form-item>
    <el-form-item
      label="权限"
    >
      <el-select
        v-model="form.role"
        placeholder="权限"
        style="width:300px"
      >
        <el-option
          v-for="item in form.roleOption"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
          <span>{{ item.value }}</span>
          <span style="float:right">
            {{ item.label }}
          </span>
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button
        style="width:140px"
        @click="back"
      >
        取消
      </el-button>
      <el-button
        type="primary"
        style="width:140px"
        @click="update"
      >
        确定
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '',
        password: '',
        role: '',
        roleOption: [
          {
            value: '1',
            label: '教务',
          },
          {
            value: '2',
            label: '财务',
          },
          {
            value: '3',
            label: '班主任',
          },
          {
            value: '4',
            label: '管理员',
          },
        ],
      },
    };
  },
  methods: {
    update() {
      if (this.form.name === '' || this.form.password === '' || this.form.role === '') {
        this.$message({
          message: '不允许为空',
          type: 'error',
        });
      } else {
        this.add();
      }
    },
    back() {
      this.$router.go(-1);
    },
    add() {
      this.$http({
        method: 'POST',
        url: '/pjsys/addTeacher.action',
        params: {
          role: this.form.role,
          username: this.form.name,
          password: this.form.password,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '成功新增',
              type: 'success',
            });
            this.$router.push('/admin/account');
          } else {
            this.$message({
              message: '新增失败',
              type: 'error',
            });
          }
        })
        .catch(() => {
          this.$message({
            message: '失败',
            type: 'error',
          });
        });
    },
  },
};
</script>

