<template>
  <div>
    <el-row
      style="margin-bottom:20px"
      align="middle"
    >
      <el-col :span="6">
        <el-select
          v-model="value"
          filterable
          placeholder="请选择班级"
          @change="search(1)"
        >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.classname"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="3">
        <p>正常:{{ zc }}</p>
      </el-col>
      <el-col :span="3">
        <p>缺勤:{{ qq }}</p>
      </el-col>
    </el-row>
    <el-table
      :data="student"
      style="width: 100%"
    >
      <el-table-column
        prop="student.stuName"
        label="姓名"
        width="100"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="student.personalNum"
        label="身份证号"
        width="160"
      />
      <el-table-column
        label="性别"
        :show-overflow-tooltip="true"
        align="center"
        width="100"
      >
        <template scope="scope">
          <span v-if="scope.row.student.sex ===1">
            男
          </span>
          <span v-if="scope.row.student.sex ===2">
            女
          </span>
        </template>
      </el-table-column>
      <el-table-column
        prop="classinfo.classname"
        :show-overflow-tooltip="true"
        align="center"
        label="班级"
        width="130"
      />
      <el-table-column
        label="操作"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,1,0)"
          >
            正常
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,1)"
          >
            病假
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,2)"
          >
            事假
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,3)"
          >
            考级或培训
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleEdit(scope.row.student.id,scope.row.classinfo.id,2,4)"
          >
            其他
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="float:right;margin-top:20px">
      <el-pagination
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
export default {
// findAllClassinfos.action
  data() {
    return {
      options: [],
      value: '',
      student: [],
      pages: 0,
      page: 1,
      zc: 0,
      qq: 0,
    };
  },
  mounted() {
    this.getClass();
  },
  methods: {
    // 获取所有的班级
    getClass() {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllClassinfos.action',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    // 按照班级切换表格
    search(page) {
      this.getzc();
      this.getqq();
      this.$http({
        method: 'POST',
        url: '/pjsys/findByclassId1.action',
        params: {
          classinfo_id: this.value,
          page,
        },
      })
        .then((res) => {
          if (res.data.datas.length > 0) {
            this.student = res.data.datas;
          } else {
            this.student = [];
            this.$message.error('该班级已经考勤完毕');
          }

          this.pages = Number(res.data.pages);
        });
    },
    getzc() {
      this.$http({
        method: 'GET',
        url: '/pjsys/CountAttences.action',
        params: {
          classinfo_id: this.value,
        },
      })
        .then((res) => {
          this.zc = res.data;
        });
    },
    getqq() {
      this.$http({
        method: 'GET',
        url: '/pjsys/CountUnAttence.action',
        params: {
          classinfo_id: this.value,
        },
      })
        .then((res) => {
          this.qq = res.data;
        });
    },
    // 换页
    handleCurrentChange(page) {
      this.page = page;
      this.search(page);
    },
    //
    handleEdit(id, classID, att, reason) {
      this.$http({
        method: 'POST',
        url: '/pjsys/attence.action',
        params: {
          stu_id: id,
          attence_status: att,
          classinfo_id: classID,
          reason_ids: reason,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '考勤成功',
              type: 'success',
            });
            this.search(this.page);
          } else {
            this.$message({
              message: '考勤失败',
              type: 'error',
            });
          }
        });
    },
  },
};
</script>

