<template>
  <div>
    <div>
      <el-button
        type="primary"
        class="btn"
        @click="add"
      >
        新建账户
      </el-button>
    </div>
    <el-table
      :data="TeacherList"
      class="table"
      v-loading="loading"
    >
      <el-table-column
        fixed
        :show-overflow-tooltip="true"
        prop="username"
        label="用户名"
        align="center"
        width="200"
      />
      <el-table-column
        fixed
        :show-overflow-tooltip="true"
        prop="password"
        label="密码"
        align="center"
        width="200"
      />
      <el-table-column
        :show-overflow-tooltip="true"
        align="center"
        label="权限"
      >
        <template scope="scope">
          <span v-if="scope.row.role ===1">
            教务
          </span>
          <span v-if="scope.row.role ===2">
            财务
          </span>
          <span v-if="scope.row.role ===3">
            班主任
          </span>
          <span v-if="scope.row.role ===4">
            管理员
          </span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"
          >
            编辑
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="账号修改"
      :visible.sync="dialogFormVisible"
    >
      <el-form
        :model="form"
      >
        <el-form-item
          label="用户名"
          label-width="100px"
        >
          <el-input
            v-model="form.name"
          />
        </el-form-item>
        <el-form-item
          label="密码"
          label-width="100px"
        >
          <el-input
            v-model="form.password"
          />
        </el-form-item>
        <el-form-item
          label="权限"
          label-width="100px"
        >
          <el-select
            v-model="form.role"
            placeholder="权限"
          >
            <el-option
              v-for="item in form.roleOption"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
              <span>{{ item.value }}</span>
              <span style="float:right">
                {{ item.label }}
              </span>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="update"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

export default {
  data() {
    return {
      dialogFormVisible: false,
      loading: true,
      TeacherList: [],
      form: {
        name: '',
        password: '',
        role: '',
        id: '',
        roleOption: [
          {
            value: '1',
            label: '教务',
          },
          {
            value: '2',
            label: '财务',
          },
          {
            value: '3',
            label: '班主任',
          },
          {
            value: '4',
            label: '管理员',
          },
        ],
      },
    };
  },
  mounted() {
    this.getLog();
  },
  methods: {
    // 获取所有账号
    getLog() {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllTeacher.action',
      })
        .then((res) => {
          this.loading = false;
          this.TeacherList = res.data;
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '获取信息失败',
            type: 'error',
          });
        });
    },
    // 换页
    handleCurrentChange(page) {
      this.getLog(page);
    },
    handleEdit(row) {
      this.dialogFormVisible = true;
      this.form.name = row.username;
      this.form.id = row.id;
      this.form.password = row.password;
      this.form.role = row.role;
    },
    // 更新账号
    update() {
      this.$http({
        method: 'POST',
        url: '/pjsys/updateTeacher.action',
        params: {
          id: this.form.id,
          username: this.form.name,
          password: this.form.password,
          role: this.form.role,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '修改成功',
              type: 'success',
            });
            this.dialogFormVisible = false;
            this.getLog();
          } else {
            this.$message({
              message: '修改失败',
              type: 'error',
            });
          }
        });
    },
    // 删除账号
    handleDelete(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteTeacher.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '删除成功',
              type: 'success',
            });
            this.getLog();
          } else {
            this.$message({
              message: '删除失败',
              type: 'error',
            });
          }
        });
    },
    // 新建账号
    add() {
      this.$router.push('/admin/addaccount');
    },
  },
};
</script>

<style scoped>
.table{
  width: 100%;
  margin-top: 20px;
}
.left{
  margin-top: 20px;
  float: right;
}
</style>
