<template>
  <div>
    <el-row>
      <el-col :span="4">
        <el-button
          type="primary"
          @click="addTeacher"
        >
          新增班主任
        </el-button>
      </el-col>
    </el-row>
    <m-table
      :teacher="teacher"
      @getTeacher="getTeacher"
    />
    <el-pagination
      style="margin-top:20px;float:right"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="page"
    />
  </div>
</template>

<script>
import MTable from './Table.vue';

export default {
  components: {
    MTable,
  },
  data() {
    return {
      teacher: [],
      total: 0,
    };
  },
  mounted() {
    this.getTeacher();
  },
  methods: {
    addTeacher() {
      this.$router.push('/admin/taddteacher');
    },
    // 获取老师
    getTeacher(page = 1) {
      this.$http({
        method: 'GET',
        url: '/pjsys/queryPageHeadMaster',
        params: {
          pageNum: page,
          pageSize: 10,
        },
      })
        .then((res) => {
          this.teacher = res.data.datas;
          this.total = res.data.total;
        });
    },
    page(page) {
      this.getTeacher(page);
    },
  },
};
</script>

