<template>
  <div>
    <el-row>
      <el-col :span="4">
        <el-input
          v-model="name"
          placeholder="请输入学生名"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          @click="back"
        >
          返回
        </el-button>
      </el-col>
    </el-row>
    <m-table
      :student="student"
      v-loading="loading"
    />
  </div>
</template>

<script>
import MTable from './SearchTable.vue';

export default {
  components: {
    MTable,
  },
  data() {
    return {
      name: '',
      student: [],
      loading: false,
    };
  },
  methods: {
    search() {
      if (this.name === '') {
        this.$message.error('请输入学生姓名');
      } else {
        this.loading = true;
        this.$http({
          method: 'GET',
          url: '/pjsys/show.action',
          params: {
            stuName: this.name,
          },
        })
          .then((res) => {
            this.loading = false;
            this.student = res.data;
          });
      }
    },
    back() {
      this.$router.go(-1);
    },
  },
};
</script>

