<template>
  <div>
    <el-row
      style="margin-bottom:20px"
      :gutter="20"
    >
      <el-col :span="4">
        <el-input
          v-model="name"
          placeholder="请输入班级名"
          @keyup.enter.native="search"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="6">
        <el-date-picker
          v-model="date"
          type="date"
          placeholder="选择日期查询"
          value-format="yyyy-MM-dd"
          @change="searchDate"
        />
      </el-col>
    </el-row>
    <el-row
      style="margin-bottom:20px"
      :gutter="20"
    >
      <el-col :span="9">
        <el-date-picker
          v-model="exportTime"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          placeholder="选择日期导出"
          value-format="yyyy-MM-dd"
        />
      </el-col>
      <el-col :span="5">
        <el-button
          type="primary"
          @click="exportDate"
        >
          按日期导出考勤数据
        </el-button>
      </el-col>
      <el-col :span="5">
        <el-button
          type="primary"
          @click="exportClass"
        >
          导出当天考勤数据
        </el-button>
      </el-col>
    </el-row>
    <m-tables :student="student" />
    <div style="float:right;margin-top:20px">
      <el-pagination
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import MTables from './AttClassTable.vue';

export default {
  components: {
    MTables,
  },
  data() {
    return {
      name: '',
      options: [],
      pages: 0,
      student: [],
      date: '',
      exportTime: [],
    };
  },
  mounted() {
    this.getStu();
  },
  methods: {
    getStu(page = 1) {
      this.$http({
        method: 'GET',
        url: '/pjsys/queryClassinfoInAttence',
        params: {
          pageNum: page,
          pageSize: 10,
          my_classname: this.name,
          dateTime: this.date,
        },
      })
        .then((res) => {
          if (res.data.datas.length > 0) {
            this.student = res.data.datas;
            this.pages = res.data.pages;
          } else {
            this.student = [];
            this.$message.error('暂无考勤数据');
          }
        })
        .catch(() => {
          this.$message.error('数据加载戳错');
        });
    },
    // 换页
    handleCurrentChange(page) {
      this.getStu(page);
    },
    // 按照班级
    search() {
      this.getStu();
    },
    searchDate() {
      this.getStu();
    },
    exportClass() {
      window.location.href = 'downloadStudentAttence';
    },
    exportDate() {
      if (this.exportTime === '') {
        this.$message.error('请选择日期');
      } else {
        window.location.href = `downloadStudentAttence?startTime=${this.exportTime[0]}&endTime=${this.exportTime[1]}`;
      }
    },
  },
};
</script>

