<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="3">
        <el-input
          v-model="name"
          disabled
        />
      </el-col>
      <el-col :span="4">
        <el-select
          v-model="subject"
          placeholder="请先选择科目"
          @change="subjectChange"
        >
          <el-option
            v-for="item in subjectOptions"
            :key="item.id"
            :label="item.subject"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-select
          v-model="Class"
          placeholder="请再选择班级"
        >
          <el-option
            v-for="item in classOptions"
            :key="item.id"
            :label="item.classname"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="selClass"
        >
          选课
        </el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          @click="back"
        >
          返回
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      subject: '',
      Class: '',
      subjectOptions: [],
      classOptions: [],
    };
  },
  mounted() {
    this.name = this.$route.params.name;
    this.getSubject();
  },
  methods: {
    selClass() {
      if (this.Class === '') {
        this.$message.error('请选择班级');
      } else {
        this.$http({
          method: 'POST',
          url: '/selectSubject.action',
          params: {
            stu_id: this.$route.params.id,
            classinfo_id: this.Class,
          },
        })
          .then((res) => {
            if (res.data === 1) {
              this.$message.success('成功选课');
              this.$emit('getInfo');
            } else {
              this.$message.error('失败');
            }
          });
      }
    },
    getSubject() {
      this.$http({
        method: 'GET',
        url: '/findAllSubject.action',
      })
        .then((res) => {
          this.subjectOptions = res.data;
        });
    },
    subjectChange(id) {
      this.getClass(id);
    },
    getClass(id) {
      this.$http({
        method: 'GET',
        url: '/findBySubjectAndAges.action',
        params: {
          stu_id: this.$route.params.id,
          subject_id: id,
        },
      })
        .then((res) => {
          this.Class = '';
          if (res.data.length <= 0) {
            this.classOptions = [];
            this.$message.error('该学生没有符合的班级');
          } else {
            this.classOptions = res.data;
          }
        });
    },
    back() {
      this.$router.push('/admin/tstudent');
    },
  },
};
</script>

