<template>
  <div>
    <el-table
      :data="teacher"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label="姓名"
        prop="name"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="性别"
        prop="sex"
        width="60"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="card"
        label="身份证号"
        :show-overflow-tooltip="true"
        align="center"
        width="160"
      />
      <el-table-column
        label="联系方式"
        prop="phonenumber"
        :show-overflow-tooltip="true"
        align="center"
        width="120"
      />
      <el-table-column
        label="备注"
        prop="comment"
        :show-overflow-tooltip="true"
        align="center"
        width="140"
      />
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"
          >
            修改
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleHaveClass(scope.row.id)"
          >
            已配置班级
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleAddClass(scope.row.id,scope.row.name)"
          >
            新增班级
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row.id)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="修改班主任"
      :visible.sync="dialogFormVisible"
      width="600px"
    >
      <el-form
        :model="form"
        label-width="120px"
      >
        <el-form-item
          label="班主任"
        >
          <el-input
            v-model="form.name"
            autocomplete="off"
            style="width:300px"
          />
        </el-form-item>
        <el-form-item
          label="身份证号"
        >
          <el-input
            v-model="form.card"
            autocomplete="off"
            style="width:300px"
          />
        </el-form-item>
        <el-form-item
          label="联系方式"
        >
          <el-input
            v-model="form.tel"
            autocomplete="off"
            style="width:300px"
          />
        </el-form-item>
        <el-form-item
          label="备注"
        >
          <el-input
            v-model="form.desc"
            autocomplete="off"
            style="width:300px"
          />
        </el-form-item>
        <el-form-item
          label="性别"
        >
          <el-radio
            v-model="form.sex"
            label="男"
          />
          <el-radio
            v-model="form.sex"
            label="女"
          />
        </el-form-item>
      </el-form>
      <div
        slot="footer"
      >
        <el-button @click="dialogFormVisible = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="editSubmit"
        >
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    teacher: [String, Array],
  },
  data() {
    return {
      dialogFormVisible: false,
      id: '',
      form: {
        name: '',
        sex: '男',
        desc: '',
        tel: '',
        card: '',
      },
    };
  },
  mounted() {
  },
  methods: {
    // 已配置班级
    handleHaveClass(id) {
      this.$router.push(`/admin/thavecalss/${id}`);
    },
    // 修改班主任
    handleEdit(row) {
      this.dialogFormVisible = true;
      this.form.name = row.name;
      this.form.sex = row.sex;
      this.form.tel = row.phonenumber;
      this.form.desc = row.comment;
      this.form.card = row.card;
      this.id = row.id;
    },
    editSubmit() {
      this.$http({
        method: 'POST',
        url: '/pjsys/updateHeadMaster',
        params: {
          id: this.id,
          name: this.form.name,
          sex: this.form.sex,
          card: this.form.card,
          phonenumber: this.form.tel,
          comment: this.form.desc,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.dialogFormVisible = false;
            this.$emit('getTeacher');
          } else {
            this.$message.success('失败');
          }
        });
    },
    // 配置班级
    handleAddClass(id, name) {
      this.$router.push(`/admin/tteachersel/${id}/${name}`);
    },
    // 删除班主任 deleteHeadMaster
    handleDel(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteHeadMaster',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getTeacher');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

