<template>
  <div>
    <el-table
      :data="student"
      style="width: 100%;margin-top:20px"
    >
      <el-table-column
        label="姓名"
        prop="student.stuName"
        sortable
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="性别"
        :show-overflow-tooltip="true"
        align="center"
        width="160"
      >
        <template slot-scope="scope">
          {{ scope.row.sex ===1?'男':'女' }}
        </template>
      </el-table-column>
      <el-table-column
        label="身份证号"
        prop="student.personalNum"
        width="190"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        label="缴费状态"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.flag ===1">
            已报名未缴费
          </span>
          <span v-if="scope.row.flag ===2">
            申请缴费
          </span>
          <span v-if="scope.row.flag ===3">
            已缴费
          </span>
          <span v-if="scope.row.flag ===4">
            退班
          </span>
          <span v-if="scope.row.flag ===5">
            申请退费
          </span>
          <span v-if="scope.row.flag ===6">
            已退费
          </span>
          <span v-if="scope.row.flag ===7">
            已报名未续费
          </span>
          <span v-if="scope.row.flag ===8">
            申请续费
          </span>
          <span v-if="scope.row.flag ===9">
            已续费
          </span>
          <span v-if="scope.row.flag ===10">
            拒绝缴费
          </span>
          <span v-if="scope.row.flag ===11">
            拒绝续费
          </span>
          <span v-if="scope.row.flag ===12">
            拒绝退费
          </span>
        </template>
      </el-table-column>
      <el-table-column
        label="备注"
        width="90"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.flags === null?'未备注':scope.row.flags }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDel(scope.row.id)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    student: [String, Array],
  },
  methods: {
    handleDel(id) {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteById.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message.success('成功');
            this.$emit('getStudent');
          } else {
            this.$message.error('失败');
          }
        });
    },
  },
};
</script>

