<template>
  <div>
    <el-row>
      <el-col :span="5">
        <el-input
          v-model="name"
          @keyup.enter.native="search"
          placeholder="请输入学生姓名进行搜索"
        />
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          @click="search"
        >
          搜索
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button @click="subject">
          根据科目
        </el-button>
      </el-col>
    </el-row>
    <m-table
      :interview="interview"
      @getInter="getInter"
      v-loading="loading"
    />
    <el-pagination
      style="margin-top:20px;float:right"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="page"
    />
  </div>
</template>

<script>
import MTable from './Table.vue';

export default {
  components: {
    MTable,
  },
  data() {
    return {
      interview: [],
      total: 0,
      name: '',
      loading: true,
    };
  },
  mounted() {
    this.getInter();
  },
  methods: {
    getInter(page = 1) {
      this.$http({
        method: 'GET',
        url: '/pjsys/findResult1.action',
        params: {
          page,
        },
      })
        .then((res) => {
          this.interview = res.data.datas;
          this.total = res.data.total;
          this.loading = false;
        });
    },
    page(page) {
      this.getInter(page);
    },
    search() {
      if (this.name === '') {
        this.getInter();
      } else {
        this.$http({
          method: 'POST',
          url: '/pjsys/show1.action',
          params: {
            stuName: this.name,
          },
        })
          .then((res) => {
            this.interview = res.data;
            this.total = 0;
          });
      }
    },
    subject() {
      this.$router.push('/admin/tclassinterviews');
    },
  },
};
</script>

