<template>
  <div>
    <el-row
      style="margin-bottom:20px"
      :gutter="20"
    >
      <el-col :span="6">
        <el-select
          v-model="value"
          filterable
          placeholder="请选择班级"
          @change="search"
        >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.classname"
            :value="item.id"
          />
        </el-select>
      </el-col>
      <el-col :span="3">
        <p>人数{{ num }}</p>
      </el-col>
    </el-row>
    <el-table
      :data="student"
      style="width: 100%"
    >
      <el-table-column
        prop="0"
        label="学生姓名"
        width="100"
        :show-overflow-tooltip="true"
        align="center"
      />
      <el-table-column
        prop="1"
        label="科目"
        width="130"
      />
      <el-table-column
        prop="2"
        :show-overflow-tooltip="true"
        align="center"
        label="班级"
        width="130"
      />
      <el-table-column

        label="申请时间"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template scope="scope">
          <span v-if="scope.row[4] === null">
            暂无审核时间
          </span>
          <span v-else>
            {{ scope.row[4] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        label="审核时间"
        :show-overflow-tooltip="true"
        align="center"
      >
        <template scope="scope">
          <span v-if="scope.row[5] === null">
            暂无审核时间
          </span>
          <span v-else>
            {{ scope.row[5] }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        label="支付方式"
        :show-overflow-tooltip="true"
        align="center"
        width="100"
      >
        <template scope="scope">
          <span v-if="scope.row[6] === 1">
            微信
          </span>
          <span v-if="scope.row[6] === 2">
            支付宝
          </span>
          <span v-if="scope.row[6] === 3">
            现金
          </span>
          <span v-if="scope.row[6] === 4">
            POS机
          </span>
          <span v-if="scope.row[6] === null">
            暂无支付方式
          </span>
        </template>
      </el-table-column>
      <el-table-column
        prop="3"
        label="金额"
        :show-overflow-tooltip="true"
        align="center"
        width="100"
      />
    </el-table>
    <m-export
      flag="6"
      :id="value"
    >
      <el-pagination
        style="float:right"
        background
        layout="prev, pager, next"
        :page-count="pages"
        @current-change="handleCurrentChange"
      />
    </m-export>
  </div>
</template>

<script>
import MExport from './Export.vue';

export default {
  components: {
    MExport,
  },
  data() {
    return {
      value: '',
      options: [],
      date: '',
      pages: 0,
      student: [],
      num: 0,
    };
  },
  mounted() {
    this.getClass();
    this.getNum(6);
  },
  methods: {
    // 获取所有班级
    getClass() {
      this.$http({
        method: 'POST',
        url: '/pjsys/findAllClassinfos.action',
      })
        .then((res) => {
          this.options = res.data;
        });
    },
    getStu(page = 1) {
      this.$http({
        method: 'POST',
        url: '/pjsys/findMoneyByClass.action',
        params: {
          classinfo_id: this.value,
          flag: 6,
          page,
        },
      })
        .then((res) => {
          this.student = res.data.datas;
          this.pages = res.data.pages;
        });
    },
    // 获取人数
    getNum(flag) {
      this.$http({
        method: 'POST',
        url: '/pjsys/findMoneyRenCount.action',
        params: {
          flag,
        },
      })
        .then((res) => {
          this.num = res.data;
        });
    },
    // 根据班级切换列表
    search() {
      this.getStu();
    },
    changeDate() {
      //
    },
    handleCurrentChange(page) {
      this.getStu(page);
    },
  },
};
</script>

