<script>
export default {
  data() {
    return {
      zc: '暂无',
      zd: '暂无',
      subject: '暂无',
      classs: '暂无',
      first: '0:0',
      second: '0:0',
      three: '0:0',
      four: '0:0',
      five: '0:0',
      six: '0:0',
      seven: '0:0',
      eight: '0:0',
    };
  },
  mounted() {
    this.getBL();
    this.getNum();
  },
  methods: {
    getBL() {
      this.$http({
        method: 'POST',
        url: '/pjsys/countpercent.action',
      })
        .then((res) => {
          this.first = res.data.first;
          this.second = res.data.second;
          this.three = res.data.three;
          this.four = res.data.four;
          this.five = res.data.five;
          this.six = res.data.six;
          this.seven = res.data.seven;
          this.eight = res.data.eight;
        });
    },
    getNum() {
      this.$http({
        method: 'GET',
        url: '/pjsys/findAll.action',
      })
        .then((res) => {
          this.zc = res.data.student;
          this.zd = res.data.studentOf;
          this.subject = res.data.subject;
          this.classs = res.data.classinfo;
        });
    },
  },
};
