<template>
  <div>
    <el-upload
      action="/pictureUpLoad"
      list-type="picture"
      :show-file-list="show"
      :on-success="success"
      :on-error="error"
    >
      <el-button
        size="small"
        type="primary"
      >
        点击上传
      </el-button>
      <div
        slot="tip"
        class="el-upload__tip"
      >
        只能上传jpg/png文件
      </div>
    </el-upload>
    <hr>
    <div>
      <el-row>
        <el-col :span="6">
          <p>当前图片上传时间:{{ time }}</p>
        </el-col>
      </el-row>
      <img
        :src="srcPic"
        alt="当前图片"
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: '',
      srcPic: '',
      show: false,
    };
  },
  mounted() {
    this.getPic();
  },
  methods: {
    getPic() {
      this.$http({
        method: 'GET',
        url: '/pjsys/firstOnePicture',
      })
        .then((res) => {
          this.srcPic = res.data.picturepath;
          this.time = res.data.uploadtime;
        });
    },
    success() {
      this.getPic();
      this.$message.success('上传成功');
    },
    error() {
      this.$message.success('上传失败,请重新尝试');
    },
  },

};
</script>

